const doNothing = () => {};

export interface SerializedNode {
  id: number;
  name: string;
  cid: number;
  type: string;
  pos: [number, number];
  size?: [number, number];
  data?: any;
  inputs?: { [id: number]: NodeInput };
  outputs?: { [id: number]: NodeOutput };
  properties?: any;
  settings?: { [name: string]: NodeSettings };
  flags?: any;
  sub_container?: SerializedContainer;
}

export interface NodeSettings {
  description: string;
  type?: SettingType;
  value?: any;
  config?: any;
  validation?: Validation;
  toJSON?: Function; // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/JSON/stringify#toJSON_behavior
}

export interface ContextMenuOption {
  title: string;

  onClick();
}

export enum BROADCAST {
  UPDATE_SETTINGS,
  UPDATE_OUTPUTS,
  UPDATE_TITLE,
  UPDATE_VALUE,
  UPDATE_STATE,
  UPDATE_NAME,
  UPDATE_PROPERTIES,
}

export enum NodeState {
  NORMAL = 'normal', // the node default colour
  INFO = 'info',
  WARNING = 'warning',
  ERROR = 'error', // when user added something that they shouldn't have
  DISABLED = 'disabled', // when a network is disabled
  ONLINE = 'online', // when a device is online
  OFFLINE = 'offline', // when a device is offline
}

export enum SettingType {
  STRING = 'string',
  NUMBER = 'number',
  BOOLEAN = 'boolean',
  READONLY = 'readonly',
  DROPDOWN = 'dropdown',
  TREE_SELECT = 'treeselect',
  CODE_AREA = 'codearea',
  PASSWORD = 'password',
  GROUP = 'group',
}

export interface NodeSettingConfig {
  groups?: Array<{
    [name: string]: {
      weight?: number;
    };
  }>;
  conditions?: {
    [name: string]: (setting: any) => boolean;
  };
}

export const convertSettingType = (type: SettingType): Type => {
  switch (type) {
    case SettingType.STRING:
      return Type.STRING;
    case SettingType.NUMBER:
      return Type.NUMBER;
    case SettingType.BOOLEAN:
      return Type.BOOLEAN;
  }
  return Type.ANY;
};

