import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';

class BoolSwitchNode extends Node {

  constructor() {
    super();
    this.title = 'Boolean Switch';
    this.description =
      "Boolean 'switch' passes corresponding Boolean input to 'output'.  If 'switch' is 'true', Boolean 'inTrue' value will be passed to 'output'  'inTrue' and 'inFalse' values can be set in settings.";

    this.addInput('switch', Type.BOOLEAN);
    this.addInput('[inTrue]', Type.BOOLEAN);
    this.addInput('[inFalse]', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    this.settings['inTrue'] = {
      description: 'True Value',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'false' },
          { value: true, text: 'true' },
          { value: null, text: 'null' },
        ],
      },
      value: null,
    };
    this.settings['inFalse'] = {
      description: 'False Value',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'false' },
          { value: true, text: 'true' },
          { value: null, text: 'null' },
        ],
      },
      value: null,
    };
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let select = this.getInputData(0);
    if (!Utils.hasInput(select)) {
      this.setOutputData(0, null);
      return;
    }
    let inA = this.getInputData(1);
    if (!Utils.hasInput(inA)) inA = this.settings['inTrue'].value;
    let inB = this.getInputData(2);
    if (!Utils.hasInput(inB)) inB = this.settings['inFalse'].value;

    if (select) this.setOutputData(0, inA);
    else this.setOutputData(0, inB);
  }
  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('switch/bool-switch', BoolSwitchNode);
