// source map for node typescript debug
require('source-map-support').install();
const logger = require('logplease').create('app', { color: 2 });
const path = require('path');
const process = require('process');
const argv = require('minimist')(process.argv.slice(2));
const dataDir = process.env['DATA_DIR'] || argv['datadir'];
const envFile = argv['envFile'] || '.env';
const envFileAbsPath = path.isAbsolute(envFile) ? envFile : path.resolve(process.cwd(), envFile);
process.env['DATA_DIR'] = path.isAbsolute(dataDir) ? dataDir : path.resolve(process.cwd(), dataDir);
require('dotenv').config({ path: envFileAbsPath });
(<any>global).__rootdirname = path.resolve(__dirname);

logger.info('----------------------------- Rubix Wires -----------------------------');
logger.info(`Process Dir       : '${process.cwd()}'`);
logger.info(`Working Dir       : '${(<any>global).__rootdirname}'`);
logger.info(`Data Dir          : '${process.env.DATA_DIR}'`);
logger.info(`Environment file  : '${envFileAbsPath}'`);

import { Backend } from './backend/backend';
import conf from './config';
import * as config from './config.json';
import { Database } from './database/database';
import { Container, Side } from './nodes/container';
import { Dashboard } from './server/dashboard';
import { Server } from './server/server';

export class App {
  db: Database;
  dashboard: Dashboard;
  rootContainer: Container;
  server: Server;

  constructor() {
    this.createServer();
    this.start();
  }

  start() {
    if (!this.rootContainer) {
      this.registerNodes();
      this.createRootContainer();
    }

    this.dashboard = new Dashboard(this.server.dashboardSocket);

    if (config.dataBase.enable) {
      if (!this.db) this.connectDatabase();
      if (this.db) this.loadDatabase(true);
    }

    if (this.rootContainer) {
      if (this.db) this.rootContainer.db = this.db;
      if (this.dashboard) this.rootContainer.dashboard = this.dashboard;
    }
    this.rootContainer.run(conf.loopInterval);
  }

  createServer() {
    this.server = new Server((<any>global).__rootdirname);
  }

  registerNodes() {
    require('./nodes/nodes/index');
    const types = Container.nodes_types ? Object.keys(Container.nodes_types).length : 0;
    logger.debug('Registered ' + types + ' nodes types');
  }

  createRootContainer() {
    this.rootContainer = new Container(Side.server);
    if (this.server.editorSocket) this.rootContainer.server_editor_socket = this.server.editorSocket.io;
    if (this.server.dashboardSocket) this.rootContainer.server_dashboard_socket = this.server.dashboardSocket.io;
  }

  connectDatabase() {
    let db;
    if (config.dataBase.useInternalDb) {
      db = require('./database/neDbDatabase').db;
    } else {
      throw 'External db not implementer yet';
    }

    this.db = db;
  }

  loadDatabase(importNodes: boolean) {
    this.db.asyncLoadDatabase().then(res => {
      this.onDbLoadSuccess(res, importNodes);
    });
  }

  startBackend() {
    new Backend(this.db).start();
  }

  private onDbLoadSuccess(res, importNodes: boolean) {
    if (!importNodes) return;

    // get last container id
    this.db.getLastContainerId((err, id) => {
      if (id) Container.last_container_id = id;
    });

    // get last node id for root container
    this.db.getLastRootNodeId((err, id) => {
      if (id) app.rootContainer.last_node_id = id;
    });

    // import nodes
    this.db.getNodes((err, ser_nodes) => {
      if (!ser_nodes) return;
      let containers = Container.containers;
      let nodesCount = 0;

      for (let n of ser_nodes) {
        let cont = containers[n.cid];
        if (!cont) cont = new Container(Side.server, n.cid);

        nodesCount++;
        cont.createNode(n.type, null, n);
      }

      // call nodes events
      for (let cid in Container.containers) {
        let container = Container.containers[cid];
        for (let id in container._nodes) {
          let node = container._nodes[id];
          if (node['onDbReaded']) node['onDbReaded']();
        }
      }

      // load dashboard ui elements
      this.dashboard.loadFromDatabase(this.db);
      let contCount = Object.keys(containers).length;
      if (containers[0]) contCount--;
      logger.info('Imported ' + contCount + ' containers, ' + nodesCount + ' nodes from database');
      setTimeout(() => this.startBackend(), 1); // run on next tick
    });
  }
}

let app = new App();
export default app;
