import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class LimitNode extends Node {

  constructor() {
    super();

    this.title = 'Limit';
    this.description =
      "'output' follows the 'input' value when it is between the 'min' and 'max' values.  If 'input' is greater than the 'max' value, 'output' will be the 'max' value.  If 'input' is less than the 'min' value, 'output' will be the 'min' value.  'max' and 'min' values can be configured from settings.";

    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('min', Type.NUMBER, 0, 'min', false);
    this.addInputWithSettings('max', Type.NUMBER, 5, 'max', false);
    this.addOutput('output', Type.NUMBER);
  }

  onCreated() {
    this.setOutputData(0, this.settings['min'].value);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const val = this.getInputData(0);

    const min = this.getInputData(1);
    const max = this.getInputData(2);

    if (val >= max) {
      this.setOutputData(0, max);
    } else if (val <= min) {
      this.setOutputData(0, min);
    } else {
      this.setOutputData(0, val);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('num-transform/limit', LimitNode);
