import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';

class AndNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicSettingsExist = false;
    this.dynamicInputsType = Type.BOOLEAN;
    this.title = 'AND';
    this.description =
      "Performs a logical 'AND' operation (output 'true' when ALL inputs are 'true'). You can specify the number of inputs in the node settings.";
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('out not', Type.BOOLEAN);
    this.setOutputData(0, false);
    this.setOutputData(1, true);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const allInputs = this.getDefinedInputValues();
    if (allInputs.length == 0) {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
      return;
    }
    const areAllInputsTrue = allInputs.every(v => v === true);
    this.setOutputData(0, areAllInputsTrue);
    this.setOutputData(1, !areAllInputsTrue);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('boolean/and', AndNode);
