import * as NeDBDataStore from 'nedb';
import config from '../../config';
import AuthUserDatabase from '../database/AuthUserDatabase';
import { AuthUser } from '../models/AuthUser';
import CryptoUtils from '../../nodes/utils/crypto-utils';

const jwt = require('jsonwebtoken');
const log = require('logplease').create('auth-database', { color: 4 });

export default class AuthUserDataSource implements AuthUserDatabase {
  private auth: NeDBDataStore;

  async asyncLoadDatabase(cleanUpInterval: any): Promise<any> {
    const path = config.dataDir;
    this.auth = new NeDBDataStore(`${path}/auth.db`);
    this.auth.persistence.setAutocompactionInterval(cleanUpInterval);
    return this.loadAuth();
  }

  private loadAuth(): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.auth.loadDatabase(err => {
        if (err) {
          log.error(err);
          reject(err);
        }
        return resolve();
      });
    });
  }

  addAuthUser(authUser: AuthUser): Promise<AuthUser> {
    return new Promise((resolve, reject) => {
      this.auth.find({}, (err, doc: Array<AuthUser>) => {
        if (doc.length) {
          log.warn('User is already inserted');
          reject('User is already inserted');
          return;
        }
        authUser.password = CryptoUtils.encrypt(authUser.password);
        this.auth.insert(authUser, (err, doc) => {
          if (err) {
            reject(err);
            log.error(err);
          } else {
            resolve(doc);
          }
        });
      });
    });
  }

  getAuthUser(): Promise<AuthUser> {
    return new Promise((resolve, reject) => {
      this.auth.findOne({}, (err, doc) => {
        if (err) {
          reject(err);
          log.error(err);
        } else {
          resolve(doc);
        }
      });
    });
  }

  deleteAuthUsers(): Promise<string> {
    return new Promise((resolve, reject) => {
      this.auth.remove({}, { multi: true }, err => {
        if (err) {
          reject(err);
          log.error(err);
        } else {
          resolve('Successfully removed');
        }
      });
    });
  }

  getAuthToken(authUser: AuthUser): Promise<string> {
    return new Promise((resolve, reject) => {
      this.auth.findOne({ username: authUser.username }, (err, doc) => {
        if (err) {
          reject(err);
          log.error(err);
        } else if (doc && CryptoUtils.decrypt(doc.password) === authUser.password) {
          const token = jwt.sign({ username: doc.username }, config.secretKey, { expiresIn: 18000 });
          resolve(`${token}`);
        } else {
          log.warn('Invalid Credentials');
          reject('Invalid Credentials');
        }
      });
    });
  }

  isAuthenticated(token: string): Promise<any> {
    return new Promise((resolve, reject) => {
      jwt.verify(token, config.secretKey, (err, decoded) => {
        if (err) {
          log.error(err);
          reject(err);
        } else {
          resolve(decoded);
        }
      });
    });
  }
}
