import { Node} from '../../node';
import { Container } from '../../container';
import Icons from '../../node-icons';
import { Type } from '../../node-io';

let logicMainColour = Icons.logicMainColour;
let logicNotIcon = Icons.logicNotIcon;

class NotNode extends Node {

  constructor() {
    super();

    this.title = 'NOT';
    this.description =
      "Performs a logical 'NOT' operation (output will be the inverse of the input).";
    // this.headerColor = logicMainColour;
    // this.iconImageUrl = logicNotIcon;
    this.addInput('input', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    this.emitTransformedInput(x => !x);
  }
}
Container.registerNodeType('boolean/not', NotNode);
