import { Container, Side } from '../../container';
import { Type } from '../../node-io';
import { UiNode } from './ui-node';

export class UiSwitchNode extends UiNode {
  constructor(container?: Container, id?: number, properties?: any) {
    super('Switch', 'UiSwitchNode', properties);

    this.description =
      "Dashboard node which displays a toggle switchon the dashboard.  When clicked it will switch the node 'output' between 'true' and 'false'.  The button text can be edited in settings.  The button can be overridden by wiring a Boolean into the node 'input'. ";
    this.title = 'Switch';
    this.setState(false);

    this.addInput('input', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
  }

  onAdded() {
    super.onAdded();
    if (this.side == Side.server) this.setOutputData(0, this.getState());
  }

  onGetMessageToServerSide(data) {
    this.setValue(data.state);
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    this.setValue(val);
  }

  setValue(val) {
    this.setOutputData(0, val);
    this.sendIOValuesToEditor();
    this.isRecentlyActive = true;

    if (this.getState() != val)
      //prevent loop sending
      this.setState(val);
  }
}

Container.registerNodeType('dashboard/switch', UiSwitchNode);
