import { isNotNull, isNull } from '../../../utils/helper';
import { Side } from '../../container';
import { Node } from '../../node';
import Icons from '../../node-icons';
import { NodeIO } from '../../node-io';

export abstract class AbstractFolderIONode extends Node {

  protected constructor(container, title: string, description: string) {
    super(container);
    this.title = title;
    this.description = description;
    this.iconImageUrl = Icons.aiIcon;
  }

  onAdd() {
    // To backward compatible
    if (!this.isUnderContainer() || isNull(this.properties['slot']) || this.properties['slot'] === -1) return;
    let slot = this.properties['slot'];
    delete this.properties['slot'];
    this.emitChange(this, 'properties');
    this.correctParentIO(this.container.container_node, slot);
  }

  abstract onCreated();

  abstract onRemoved();

  abstract onAfterSettingsChange();

  protected abstract correctParentIO(containerNode: Node, slot: number);

  protected isUnderContainer() {
    return isNotNull(this.container.container_node);
  }

  protected emitChange(node: Node, ...properties) {
    if (node.side === Side.server) {
      let s_node = node.serialize();
      let updated = properties.reduce((o, prop) => ((o[prop] = s_node[prop]), o), {});
      node.container.db.updateNode(node.id, node.container.id, { $set: updated });
    }
  }

  protected findParentOutputById(containerNode: Node, inputOrOutput: string) {
    return Object.entries(containerNode[inputOrOutput])
                 .find(([_, io]) => this.id === (<NodeIO>io).subNodeId) ?? [null, null];
  }

}
