import * as moment from 'moment-timezone';
import { Node, SettingType} from '../../node';
import { Container, Side } from '../../container';
import { Type } from '../../node-io';
import { ScheduleUtils } from './schedule-utils';
import TimeUtils from '../../utils/time-utils';

/**
 * ScheduleChecker checks the schedule is active or not on current time
 * This node is for checking a formatted schedule JSON: SCHEDULE_MODE.INPUT
 * https://en.wikipedia.org/wiki/List_of_tz_database_time_zones
 */
export class ScheduleChecker extends Node {
  nextStart;
  nextStop;
  schedulePayload;
  constructor() {
    super();
    this.title = 'Schedule Checker (unconfigured)';
    this.description =
      'This node checks the API-Schedule output string for active schedules that match the selected ‘Schedule Name’ setting.  ‘output’ will be ‘true’ when the matching schedule is active and ‘enable’ is ‘true’; otherwise ‘output’ will be ‘false’.  ‘nextStart’ is the timestamp output of the next scheduled start time.  ‘nextStop’ is the timestamp output of the next scheduled stop time.  ‘lastCheck’ is the timestamp output of the last time a schedule check was run.  ‘error’ output will be ‘true’ if there is an error while checking the schedule input string; otherwise ‘error’ will be false.  The frequency that schedules are checked can be modified with the ‘Schedule Check Interval’ setting (set in millis).  The ‘Timestamp Output Format’ setting can be adjusted to output timestamps as strings or as Epoch Timestamps.  String timestamps will be displayed in the timezone specified by the ‘Timezone’ setting.';
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable', false);
    this.addInput('schedules', Type.JSON);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('payload', Type.BOOLEAN);
    this.addOutput('nextStart', Type.STRING);
    this.addOutput('nextStop', Type.STRING);
    this.addOutput('lastCheck', Type.STRING);
    this.addOutput('error', Type.STRING);
    /*
    this.settings['scheduleName'] = {
      description: 'Select Schedule',
      type: SettingType.DROPDOWN,
      config: {
        items: [],
      },
      value: '',
    };
    this.settings['includeStringName'] = {
      description: "Use 'Schedule Name String' from Below",
      value: false,
      type: SettingType.BOOLEAN,
    };
    */

    this.settings['stringScheduleName'] = {
      //description: 'Schedule Name String',
      description: 'Schedule Name',
      value: '',
      type: SettingType.STRING,
    };

    this.settings['timezone'] = {
      description: 'TIMEZONE: Enter the timezone',
      value: moment.tz.guess(),
      type: SettingType.STRING,
    };

    this.settings['interval'] = {
      description: 'Schedule Check Interval (minimum 10 seconds)',
      value: 30,
      type: SettingType.NUMBER,
    };

    this.settings['time'] = {
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'seconds',
    };
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });

    this.settings['numericTimeFormat'] = {
      description: 'Timestamp Output Format',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'Timestamps as Strings' },
          { value: true, text: 'Timestamps as Epoch Numerics' },
        ],
      },
      value: false,
    };

    this.nextStart = null;
    this.nextStop = null;
    this.schedulePayload = null;
    this.EXECUTE_INTERVAL = 10000;
  }

  async onAdded() {
    await this.updateTitle();
    await this.onInputUpdated();
  }

  async onInputUpdated() {
    if (this.side !== Side.server) return;
    let input = this.getInputData(1) || { weekly: {}, events: {} };

    /*  FOR USE WITH DROPDOWN SCHEDULE SELECTION
    let weeklyInput = input['weekly'];
    let eventsInput = input['events'];
    console.log(weeklyInput, 'schedule weeklyInput');
    console.log(eventsInput, 'schedule eventsInput');
    const scheduleNameItems = [];
    weeklyInput &&
      Object.keys(weeklyInput).forEach(key => {
        scheduleNameItems.push(weeklyInput[key].name);
      });
    eventsInput &&
      Object.keys(eventsInput).forEach(key => {
        if (!scheduleNameItems.includes(eventsInput[key]['name'])) {
          scheduleNameItems.push(eventsInput[key]['name']);
        }
      });
    // What if the intended schedule isn't in the current JSON when configuring? Can set a 'Schedule Name String' from settings.
    if (
      this.settings['includeStringName'].value &&
      this.settings['stringScheduleName'].value != '' &&
      !scheduleNameItems.includes(this.settings['stringScheduleName'].value)
    ) {
      scheduleNameItems.push(this.settings['stringScheduleName'].value);
    }
    console.log(scheduleNameItems, 'scheduleNameItems');
    let settingsItems = [];
    scheduleNameItems.forEach(scheduleName => {
      settingsItems.push({
        value: scheduleName,
        text: scheduleName,
      });
    });
    this.settings['scheduleName'].config.items = settingsItems;
    this.broadcastSettingsToClients();
    if (scheduleNameItems.length == 0) {
      this.settings['scheduleName'].value = null;
      this.setOutputData(0, false, true);
      this.setOutputData(1, null, true);
      this.setOutputData(2, null, true);
      this.setOutputData(3, null, true);
      this.setOutputData(4, moment(this.executeLastTime).format('YYYY-MM-DDTHH:MM:ss'), true);
      this.setOutputData(5, true, true);
      console.log('Schedule Checker Error: no schedule selected');
      return;
    } else if (!scheduleNameItems.includes(this.settings['scheduleName'].value)) {
      this.settings['scheduleName'].value = null;
      this.setOutputData(0, false, true);
      this.setOutputData(1, null, true);
      this.setOutputData(2, null, true);
      this.setOutputData(3, null, true);
      this.setOutputData(4, moment(this.executeLastTime).format('YYYY-MM-DDTHH:MM:ss'), true);
      this.setOutputData(5, false, true);
      return;
    }
    */

    await this.setOutput(input);
  }

  async onExecute() {
    await this.setOutput();
  }

  async updateTitle() {
    if (
      this.settings['stringScheduleName'].value != null &&
      this.settings['stringScheduleName'].value != ''
    ) {
      this.title = `Schedule Checker (${this.settings['stringScheduleName'].value})`;
    } else this.title = 'Schedule Checker (unconfigured)';
    this.broadcastTitleToClients();
  }

  async onAfterSettingsChange() {
    await this.updateTitle();
    let interval = this.settings['interval'].value;
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.EXECUTE_INTERVAL = Math.max(interval, 10000);
    await this.onInputUpdated();
  }

  private async setOutput(input = null) {
    this.nextStart = null;
    this.nextStop = null;
    this.schedulePayload = null;
    if (!input) input = this.getInputData(1) || { weekly: {}, events: {} };
    console.log('input1', input)


    //const key = this.settings['scheduleName'].value;   FOR USE WITH DROPDOWN SCHEDULE SELECTION
    const key = this.settings['stringScheduleName'].value;
    console.log('key', key)
    if (key == null || key === '') {
      this.setOutputData(0, false);
      this.setOutputData(1, null);
      this.setOutputData(2, null);
      this.setOutputData(3, null);
      this.setOutputData(4, moment(this.executeLastTime).format('YYYY-MM-DDTHH:MM:ss'));
      this.setOutputData(5, 'Schedule Checker Error: invalid schedule selected');
      return;
    }

    //const now = moment();
    let timeZone = this.settings['timezone'].value;
    if (!moment.tz.zone(timeZone)) {
      timeZone = moment.tz.guess();
    }
    const now = moment.tz(timeZone);
    console.log('now', now.format())
    //const now = moment('2020-01-21T07:00:00.000+05:00')
    //const now = moment.tz("2020-03-27T01:30", 'Canada/Pacific');    //for testing
    //const now = moment.tz("2020-03-26T12:30", 'Australia/Sydney');  //for testing
    //const now = moment.tz("2020-09-25T04:30", timeZone);            //for testing
    //console.log("now", now.format())

    const isEnabled = this.getInputData(0);
    if (input && key && isEnabled) {
      let weeklyOutput = false;
      let weeklyScheduleOutput = null;
      for (var sched in input['weekly']) {
        if (input['weekly'][sched]['name'] === key) {
          weeklyScheduleOutput = ScheduleUtils.checkWeeklySchedule(input['weekly'][sched], now); // returns {scheduleStatus, nextStart, nextStop value}
          weeklyOutput = weeklyScheduleOutput.scheduleStatus || weeklyOutput;
          this.schedulePayload = weeklyScheduleOutput.value || this.schedulePayload;
          if (weeklyScheduleOutput.nextStop != null) {
            if (this.nextStop == null || weeklyScheduleOutput.nextStop.isBefore(this.nextStop)) {
              this.nextStop = weeklyScheduleOutput.nextStop;
            }
          }
          if(weeklyScheduleOutput.nextStart != null){
            if (this.nextStart == null || weeklyScheduleOutput.nextStart.isBefore(this.nextStart)) {
              this.nextStart = weeklyScheduleOutput.nextStart;
            }
          }
        }
      }

      let eventsOutput = false;
      let eventScheduleOutput = null;
      for (var sched in input['events']) {
        if (input['events'][sched]['name'] === key) {
          eventScheduleOutput = ScheduleUtils.checkEventsSchedule(input['events'][sched], now); // returns {scheduleStatus, nextStart, nextStop, value}
          eventsOutput = eventScheduleOutput.scheduleStatus || eventsOutput;
          this.schedulePayload = eventScheduleOutput.value || this.schedulePayload;
          if (eventScheduleOutput.nextStop != null) {
            if (eventScheduleOutput.nextStop.isBefore(this.nextStop) || this.nextStop == null)
              this.nextStop = eventScheduleOutput.nextStop;
          }
          if (eventScheduleOutput.nextStart != null) {
            if (eventScheduleOutput.nextStart.isBefore(this.nextStart) || this.nextStart == null)
              this.nextStart = eventScheduleOutput.nextStart;
          }
        }
      }
      const output = weeklyOutput || eventsOutput;

      this.setOutputData(0, output);
      this.setOutputData(1, this.schedulePayload);
      if (this.nextStart != null) {
        if (!this.settings['numericTimeFormat'].value)
          this.setOutputData(2, this.nextStart.tz(timeZone).format());
        else this.setOutputData(2, this.nextStart.valueOf());
      } else this.setOutputData(2, null);

      if (this.nextStop != null) {
        if (!this.settings['numericTimeFormat'].value)
          this.setOutputData(3, this.nextStop.tz(timeZone).format());
        else this.setOutputData(3, this.nextStop.valueOf());
      } else this.setOutputData(3, null);
    } else {
      this.setOutputData(0, false);
      this.setOutputData(1, null);
      this.setOutputData(2, null);
      this.setOutputData(3, null);
    }
    if (!this.settings['numericTimeFormat'].value) {
      this.setOutputData(
        4,
        moment(this.executeLastTime)
          .tz(timeZone)
          .format(),
      );
    } else this.setOutputData(4, moment(this.executeLastTime).valueOf());
    this.setOutputData(5, false);
  }
}
Container.registerNodeType('schedule/schedule-checker', ScheduleChecker);
