import { Node } from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import { isNull } from '../../../utils/helper';

class JsonParse extends Node {
  constructor() {
    super();
    this.title = 'Json Parse';
    this.description = 'Return JSON.stringify or a JSON.parse';
    this.addInput('json-input', Type.JSON);
    this.addOutput('output', Type.JSON);
  }

  onInputUpdated() {
    let input = this.getInputData(0);
    if (isNull(input)) return;
    try {
      this.setOutputData(0, input);
    } catch (err) {
      this.debugInfo(`JSON PARSE: try/catch , ${err}`);
    };
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('json/json-parse', JsonParse);
