import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class FiltersOnlyFalseNode extends Node {
  constructor() {
    super();

    this.title = 'Only False';
    this.description =
      "This node filters 'input' values.  Only 'false' values are passed to 'output'.";

    this.addInput('input', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
  }

  onAdded(){
    this.onInputUpdated();
  }

  onInputUpdated() {
    if (this.getInputData(0) === false) this.setOutputData(0, false);
  }
}
Container.registerNodeType('filter/only-false', FiltersOnlyFalseNode);
