import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';

class AvgNode extends Statistics {
  constructor() {
    super();
    this.title = 'Average';
    this.description =
      'Outputs the Average, Count, and Sum values of all the (non null) Numeric inputs. The number of inputs and their values can be modified from settings.';
    this.addOutput('Avg', Type.NUMBER);
    this.addOutput('Count', Type.STRING);
    this.addOutput('Sum', Type.STRING);
  }

  onInputUpdated() {
    const data = this.getDefinedInputsOrSettingsValues();
    const count = data.length;
    const sum = Utils.sum(data);
    let avg = 0;
    if (count !== 0) {
      avg = sum / count;
    }

    this.setOutputData(0, avg);
    this.setOutputData(1, count);
    this.setOutputData(2, sum);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('statistics/avg', AvgNode);
