import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import { FlexibleNode } from '../../flexible-node';

class StringSelectNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicInputsType = Type.STRING;
    super.dynamicInputStartPosition = 1;
    this.title = 'String Select';
    this.description =
      "Numeric integer 'select' passes corresponding String input to 'output'.  If 'select' is 2, 'in 2' will be passed to 'output' (likewise for other integer 'select' values). If the 'select' value does not have a corresponding 'in #' value, 'output' value will be 'null'.  The number of inputs and their values can be modified from settings.";
    this.addInput('select', Type.NUMBER);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
 
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const select = this.getInputData(0);
    if (
      !Utils.hasInput(select) ||
      select < this.dynamicInputStartPosition ||
      select > this.getInputsCount() - this.dynamicInputStartPosition
    ) {
      this.setOutputData(0, null);
      return;
    }
    this.setOutputData(0, this.getInputData(select));
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('switch/string-select', StringSelectNode);
