import { SettingType, Node } from '../../../node';
import { Container, Side } from '../../../container';
import { Type } from '../../../node-io';
import Utils from '../../../utils';
import axios from 'axios';
import TimeUtils from '../../../utils/time-utils';
import Edge28Utils from './edge-utils';
import { edgeIp, edgePort, edgeApiVer } from './edge-constant';

export class Edge28LORAResetNode extends Node {
  lastSendTime;
  lastTrigger;
  timeoutFunc;

  constructor() {
    super();
    this.title = 'Edge IO 28 LORA Connect Reset';
    this.description =
    `## Description\n ` +
    ` The is node is used in conjunction with the ***edge-io-28*** when using the ***lora-connect***. This node uses an internal rest-api to rest a ***LORA Connect gateway*** connected to an ***edge-io-28*** controller\n ` +
    `   \n ` +
    `## Description\n ` +
    `   \n ` +
    ` The reason for this nodes is to give the user more control over the nube hardware\n ` +
    ` When a ***trigger*** is activated on the node input or the ***interval*** timer is triggered the lora-modules will be reset\n ` +
    ` The reboot of the module only takes ***1000ms*** to restart\n ` +

    this.addInputWithSettings('enableInterval', Type.BOOLEAN, false, 'Enable Interval Reset');
    this.addInputWithSettings('interval', Type.NUMBER, 15, 'Interval');
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('lastReset');

    this.settings['time'] = {
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'minutes',
    };
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  async writePointValue(
    host: string,
    port: number,
    apiVer: string,
    pointType: string,
    pointId: string,
    val: number,
    priority: number,
  ): Promise<any> {
    const url = `${Utils.buildUrl(host, port)}/api/${apiVer}/write/${pointType}/${pointId}/${val}/${priority}`;
    //console.log(url);
    const pointValue = await axios.get(url);
    //console.log('writePointValue()', pointValue)
    return pointValue.data;
  }

  onAdded() {
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    if (this.side !== Side.server) return;
    let interval = this.getInputData(1);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.EXECUTE_INTERVAL = interval;
    try {
      Edge28Utils.addPoint(this.getParentNode(), this);
    } catch (error) {}
  }

  onExecute() {
    if (this.side !== Side.server) return;
    if (!this.getInputData(0)) return;
    this.resetLORAConnect();
  }

  onInputUpdated() {
    if (this.side !== Side.server) return;
    let interval = this.getInputData(1);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    this.EXECUTE_INTERVAL = interval;
    let trigger = this.getInputData(2);
    //if (trigger && this.inputs[2].updated) {
    if (trigger && !this.lastTrigger) this.resetLORAConnect();
    this.lastTrigger = trigger;
  }

  resetLORAConnect() {
    if (this.side !== Side.server) return;
    const pointType = 'do';
    const pointNumber = 'lc';
    this.writePointValue(edgeIp, edgePort, edgeApiVer, pointType, pointNumber, 0, 16)
      .then(e => {
        this.setOutputData(0, new Date().valueOf(), true);
        this.timeoutFunc = setTimeout(() => {
          this.writePointValue(edgeIp, edgePort, edgeApiVer, pointType, pointNumber, 1, 16);
        }, 2000);
      })
      .catch(err => this.debugInfo(`ERROR: LoRa Edge-28 Reset - ${err}`));
  }

  onAfterSettingsChange() {
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    if (this.side !== Side.server) return;
    this.onInputUpdated();
  }
}

Container.registerNodeType('protocols/nube/edge-28-lora-reset', Edge28LORAResetNode);
