import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class MathCeilNode extends Node {

  constructor() {
    super();

    this.title = 'Ceil';
    this.description = "'output' is the 'input' value rounded UP to the nearest integer.";

    this.addInput('input', Type.NUMBER);
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    this.emitTransformedInput(x => Math.ceil(x));
  }
}
Container.registerNodeType('num-transform/ceil', MathCeilNode);
