import { Node, SettingType} from '../../node';
import { Container, Side } from '../../container';
import { Type } from '../../node-io';
let moment = require('moment-timezone');

//https://github.com/travishorn/csval
//https://github.com/C2FO/fast-csv

class CSVMergeRowsNode extends Node {
  obj = [];
  constructor() {
    super();
    this.title = 'CSV Merge Rows';
    this.description =
      'This node merges the rows of properly formatted CSV strings.  ‘merge’ CSV string will be joined with the ‘original’ CSV string.  The ‘merge’ CSV rows will be added below the ‘original’ CSV rows.  Only the ‘original’ header titles will be kept.  No error checking.';
    this.addInput('original', Type.STRING);
    this.addInput('merge', Type.STRING);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
    this.setOutputData(0, '');
    this.onInputUpdated();
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    if (!this.getInputData(0) || !this.getInputData(1)) return;
    let outputCSV = this.getInputData(0).slice(0, this.getInputData(0).length - 1);
    let addCSV = this.getInputData(1);
    addCSV = addCSV.slice(addCSV.indexOf('\n'), addCSV.length - 1);
    outputCSV += addCSV;

    this.setOutputData(0, outputCSV);
  }
}
Container.registerNodeType('csv/csv-merge-rows', CSVMergeRowsNode);
