import { Container } from '../../container';
import { Node} from '../../node';
import { Type } from '../../node-io';

class BoolToIntNode extends Node {
    constructor() {
        super();
        this.title = 'Bool-to-Int Conversion';
        this.description = 'This node converts a Boolean to a Number.';
        this.addInput('input', Type.BOOLEAN);
        this.addOutput('output', Type.NUMBER);
    }
    onInputUpdated() {
        this.setOutputData(0, this.inputs[0].data ? 1 : 0);
    }
}

Container.registerNodeType('conversion/bool-to-int', BoolToIntNode);

class IntToBoolNode extends Node {
    constructor() {
        super();
        this.title = 'Int-to-Bool Conversion';
        this.description = 'This node converts a Number to a Boolean.';
        this.addInput('input', Type.NUMBER);
        this.addOutput('output', Type.BOOLEAN);
    }
    onInputUpdated() {
        this.setOutputData(0, !!this.inputs[0].data);
    }
}
Container.registerNodeType('conversion/int-to-bool', IntToBoolNode);


class StringToBool extends Node {
    constructor() {
        super();
        this.title = 'String-to-Bool Conversion';
        this.description = 'This node converts a Number to a Boolean.';
        this.addInput('input', Type.STRING);
        this.addOutput('output', Type.BOOLEAN);
    }
    onInputUpdated() {
        const val = this.getInputData(0)
        if (val === "0" || val === 'false') {
            this.setOutputData(0, false, true);
        } else if (val === "1" || val === 'true') {
            this.setOutputData(0, true, true);
        }
    }
}
Container.registerNodeType('conversion/string-to-bool', StringToBool);
