import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathSqrtNode extends Node {
  constructor() {
    super();

    this.title = 'Sqrt';
    this.description = "'sqrt(x)' is the Square Root of input 'x'.";
    this.addInput('x', Type.NUMBER);
    this.addOutput('sqrt(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.sqrt(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/sqrt', MathSqrtNode);
