import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathCbrtNode extends Node {
  constructor() {
    super();

    this.title = 'Cbrt';
    this.description = "'cbrt(x)' is the Cubic Root of input 'x'.";
    this.addInput('x', Type.NUMBER);
    this.addOutput('cbrt(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (val != null) this.setOutputData(0, Math.cbrt(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/cbrt', MathCbrtNode);
