import * as NeDBDataStore from 'nedb';

import AppDatabase from '../database/AppDatabase';
import config from '../../config';

const log = require('logplease').create('database', { color: 4 });

export default class AppDataSource implements AppDatabase {
  private app: NeDBDataStore;

  async asyncLoadDatabase(cleanUpInterval: any): Promise<any> {
    const path = config.dataDir;
    this.app = new NeDBDataStore(`${path}/app.db`);
    this.app.persistence.setAutocompactionInterval(cleanUpInterval);
    return this.loadApp();
  }

  private loadApp(): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.app.loadDatabase(err => {
        if (err) {
          log.error(err);
          reject(err);
        }
        return resolve();
      });
    });
  }

  getLastContainerId(callback?: (err?: Error, id?: number) => void) {
    this.app.findOne({ _id: 'lastContainerId' }, function(err, doc: any) {
      if (err) log.error(err);
      if (callback) {
        if (doc) callback(err, doc.last);
        else callback(err, null);
      }
    });
  }

  updateLastContainerId(id: number, callback?: (err?: Error) => void) {
    this.app.update({ _id: 'lastContainerId' }, { $set: { last: id } }, { upsert: true }, function(
      err,
      updated,
    ) {
      if (err) log.error(err);
      if (updated == 0) log.error("Cat't update. Document not found.");
      if (callback) callback(err);
    });
  }

  getLastRootNodeId(callback?: (err?: Error, id?: number) => void) {
    this.app.findOne({ _id: 'lastRootNodeId' }, function(err, doc: any) {
      if (err) log.error(err);
      if (callback) {
        if (doc) callback(err, doc.last);
        else callback(err, null);
      }
    });
  }

  updateLastRootNodeId(id: number, callback?: (err?: Error) => void) {
    this.app.update({ _id: 'lastRootNodeId' }, { $set: { last: id } }, { upsert: true }, function(
      err,
      updated,
    ) {
      if (err) log.error(err);
      if (updated == 0) log.error("Cat't update. Document not found.");
      if (callback) callback(err);
    });
  }

  dropApp(callback?: (err?: Error) => void) {
    this.app.remove({}, { multi: true }, function(err) {
      if (err) log.error(err);
      if (callback) callback(err);
    });
  }
}
