import { Node, SerializedNode } from './node';
import { Renderer } from '../client/editor/renderer';
import { ContainerNode } from './container-node';
import { Database } from '../database/database';
import Utils from './utils';
import { Dashboard } from '../server/dashboard';
import ContainerUtils from './container-utils';
import Registry from './registry';
import Namespace = SocketIO.Namespace;
import { get, uniq } from 'lodash';

const log = require('logplease').create('container', { color: 5 });
const doNothing = () => {};

export interface SerializedContainer {
  id: number;
  last_node_id: number;
  config?: any;
  name: string;
  serializedNodes?: Array<SerializedNode>;
}

export enum Side {
  server,
  editor,
  dashboard,
}

export class Container {
  static nodes_types: { [type: string]: any } = {};
  static containers: { [id: number]: Container } = {};
  static last_container_id: number = -1;

  static rootContainer(): Container {
    return this.containers[0];
  }

  parent_container_id?: number;
  parent_id?: number;
  container_node: ContainerNode;

  client_socket: SocketIOClient.Socket; //SocketIOClient.Socket|SocketIO.Server;
  server_dashboard_socket: Namespace;
  server_editor_socket: Namespace;

  db: Database;
  dashboard: Dashboard;

  _nodes: { [id: number]: Node } = {};

  id: number;
  name: string;
  side: Side;
  renderers: Array<Renderer>;
  isRunning: boolean;
  last_node_id: number;
  iteration: number;
  tag?: string;
  config: {
    links_ontop?: boolean;
  };

  globaltime: number;
  runningtime: number;
  fixedtime: number;
  fixedtime_lapse: number;
  elapsed_time: number;
  starttime: number;
  execution_timer_id: number | any;

  onStopEvent: Function;
  onExecuteStep: Function;
  onAfterExecute: Function;
  onPlayEvent: Function;
  frame: number;

  constructor(side: Side, id?: number, properties?: any) {
    if (typeof side !== 'number') throw 'Container side is not defined';

    this.renderers = null;

    this.id = id || ++Container.last_container_id;

    if (this.id === 0) this.name = 'Main';

    this.side = side;

    Container.containers[this.id] = this;
    this.clear();
    this.debug('Container created');

    let rootContainer = Container.containers[0];
    if (rootContainer) {
      if (rootContainer.server_dashboard_socket) this.server_dashboard_socket = rootContainer.server_dashboard_socket;

      if (rootContainer.server_editor_socket) this.server_editor_socket = rootContainer.server_editor_socket;

      if (rootContainer.client_socket) this.client_socket = rootContainer.client_socket;

      if (rootContainer.db) this.db = rootContainer.db;

      if (rootContainer.dashboard) this.dashboard = rootContainer.dashboard;
    }
  }

  static clear() {
    Container.containers = {};
    Container.last_container_id = -1;
  }

  /**
   * Register a node class so it can be listed when the user wants to create a new one
   * @param type name of the node and path
   * @param node_class class containing the structure of a node
   * @param parentContainerNodeType some menus should be shown only if parentContainerNodeType is there
   * @param show_in_menu
   * @param recursive same container node inside the parent container node
   * @param alwaysInsideContainer
   */
  static registerNodeType(
    type: string,
    node_class: any,
    parentContainerNodeType: string = null,
    show_in_menu: boolean = true,
    recursive: boolean = false,
    alwaysInsideContainer: boolean = false,
  ): void {
    if (!(node_class.prototype instanceof Node))
      throw `Can't register node of type [${type}]. Class must inherit Node base class!`;
    let node: Node = new node_class(this);

    node_class.type = type;
    node_class.category = type.substr(0, type.lastIndexOf('/'));
    node_class.node_name = type.substr(type.lastIndexOf('/') + 1, type.length);
    node_class.show_in_menu = show_in_menu;
    node_class.icon = node.iconImageUrl;
    node_class.color = node.headerColor;
    node_class.title = node.title || node.type;
    node_class.parentContainerNodeType = parentContainerNodeType;
    node_class.recursive = recursive;
    node_class.onlyInsideContainer = alwaysInsideContainer;

    this.nodes_types[type] = node_class;
  }

  static createNode(type: string, container: Container, position: [number, number]): Node {
    const node_class = this.nodes_types[type];
    if (node_class) {
      const node: Node = new node_class(this, container, -1000);
      node.type = type;
      node.pos = position;
      node.size = node.computeSize(true);
      return node;
    } else {
      throw `Can't create node of type [${type}]. Make sure it is registered through registerNodeType`;
    }
  }

  /**
   * Returns a list of node types matching one category
   * @param category category name
   * @returns {Array} array with all the node classes
   */
  static getNodeTypesInCategory(category: string): Array<any> {
    return Object.values(this.nodes_types).filter(n => n.category == (category || null));
  }

  static getNodeTypes(): Array<any> {
    return Object.values(this.nodes_types);
  }

  static nodeIsInCategory(category: string) {
    return n => n.category == (category || null);
  }

  /**
   * Returns a list with all the node type categories
   * @returns {Array} array with all the names of the categories
   */
  static getNodeTypesCategories(): Array<any> {
    return uniq(
      Object.values(this.nodes_types)
        .filter(n => !!n.category && n.show_in_menu)
        .map(n => n.category)
        .concat(['']),
    );
  }

  /**
   * Removes all nodes from this container
   */
  clear(): void {
    this.stop();
    this.isRunning = false;
    this.last_node_id = 0;

    //nodes
    this._nodes = {};

    //iterations
    this.iteration = 0;

    this.config = {};

    //timing
    this.globaltime = 0;
    this.runningtime = 0;
    this.fixedtime = 0;
    this.fixedtime_lapse = 0.01;
    this.elapsed_time = 0.01;
    this.starttime = 0;

    this.sendActionToRenderer('clear');
  }

  /**
   * Stops the execution loop of the container
   */
  stop(): void {
    if (!this.isRunning) return;

    this.isRunning = false;

    if (this.onStopEvent) this.onStopEvent();

    if (this.execution_timer_id != null) clearInterval(this.execution_timer_id);
    this.execution_timer_id = null;

    for (let id in this._nodes) {
      let node = this._nodes[id];
      if (node['onStopContainer']) node['onStopContainer']();
    }
  }

  /**
   * Attach renderer to this container
   * @param renderer
   */
  attachRenderer(renderer: Renderer): void {
    if (renderer.container && renderer.container != this) {
      renderer.deselectAllNodes();
      renderer.container.detachRenderer(renderer);
    }

    renderer.container = this;
    if (!this.renderers) this.renderers = [];
    this.renderers.push(renderer);

    //reset active nodes
    for (let i in renderer.container._nodes) {
      let node = renderer.container._nodes[i];
      node.boxcolor = renderer.theme.NODE_DEFAULT_BOXCOLOR;
      node.setDirtyCanvas(true, true);
    }
  }

  /**
   * Detach renderer from this container
   * @param renderer
   */
  detachRenderer(renderer: Renderer): void {
    if (!this.renderers) return;

    let pos = this.renderers.indexOf(renderer);
    if (pos == -1) return;

    renderer.container = null;

    this.renderers.splice(pos, 1);
    if (this.renderers.length == 0) delete this.renderers;
  }

  /**
   * Starts running this container every interval milliseconds.
   * @param interval amount of milliseconds between executions
   */
  //STARTS from here 
  run(interval: number = 1): void {
    if (this.isRunning) return;

    this.isRunning = true;

    if (this.onPlayEvent) this.onPlayEvent();

    for (let id in this._nodes) {
      let node = this._nodes[id];
      if (node['onRunContainer']) node['onRunContainer']();
    }

    //launch
    this.starttime = Utils.getTime();
    let that = this;

    this.execution_timer_id = setInterval(function() {
      //execute
      that.runStep(1);
    }, interval);//on each loop interval this runStep gets calls for 1 time, currently the inter == 100ms
  }

  /**
   * Run N steps (cycles) of the container
   * @param steps number of steps to run, default is 1
   */
  // THIS block gets called on LOOP_INTERVAL, CURRENTLY this is 100ms on .env and whatever comes in this 100ms inside, those outputs gets ignored, coz we only read value on 100ms
  runStep(steps: number = 1): void {
    let start = Utils.getTime();
    this.globaltime = 0.001 * (start - this.starttime);

    for (let i = 0; i < steps; i++) {
      this.transferDataBetweenNodes(); // this one is the main block where we are doing all operation to see whether our in/out got changed or not //main block to calculate whether our in/out got changed or not

      let now = Date.now();

      for (let id in this._nodes) {
        let node = this._nodes[id];

        // on each 100ms we are looping on this block, and we are seeing whether that interval is exceeded to EXECUTE_INTERVAL or not, if exceeded then call onExecute, so this block is only useful for the operation
        // which should be greater than 100ms to perform some kind of action
        if (
          !node.EXECUTE_INTERVAL ||
          (node.EXECUTE_INTERVAL && !node.executeLastTime) ||
          (node.executeLastTime && now - node.executeLastTime >= node.EXECUTE_INTERVAL)
        ) {
          if (node['onExecute']) node['onExecute']();

          if (node.EXECUTE_INTERVAL) node.executeLastTime = now;
        }

        // this updated is came from transferDataBetweenNodes
        // these UPDATE_INPUTS_INTERVAL is also plays roles as Debounce, normally we don't have value on the node for this variable, so it directly enters into the block
        if (node.isUpdated) {
          if (
            !node.UPDATE_INPUTS_INTERVAL ||
            (node.UPDATE_INPUTS_INTERVAL && !node.updateInputsLastTime) ||
            (node.updateInputsLastTime && now - node.updateInputsLastTime >= node.UPDATE_INPUTS_INTERVAL)
          ) {
            if (node['onInputUpdated']) node['onInputUpdated']();//here we get called our onInputUpdated

            node.isUpdated = false;

            for (let i in node.inputs) if (node.inputs[i].updated) node.inputs[i].updated = false;

            if (node.UPDATE_INPUTS_INTERVAL) node.updateInputsLastTime = now; //this is for restricting multiple values on that interval and to act like Debounce
          }
        }
      }

      this.fixedtime += this.fixedtime_lapse;

      if (this.onExecuteStep) this.onExecuteStep();
    }

    if (this.onAfterExecute) this.onAfterExecute();

    let elapsed = Utils.getTime() - start;
    if (elapsed == 0) elapsed = 1;
    this.elapsed_time = 0.001 * elapsed;
    this.globaltime += 0.001 * elapsed;
    this.iteration += 1;
  }

  transferDataBetweenNodes() {
    // it runs on each 100ms interval so
    //all nodes
    for (let id in this._nodes) {
      let node = this._nodes[id];
      if (!node.outputs) continue;

      //all outputs
      for (let o in node.outputs) {
        let output = node.outputs[o];
        if (!output.updated) continue;// when updated is true from output of each node we dont break the loop and it will go down

        output.updated = false; // make update false, hence we will not get as updated=true on next loop

        if (output.links == null) continue;//if links is not no need to do any operation

        //all output links
        for (let link of output.links) { //loop on all links 
          let target_node = this._nodes[link.target_node_id]; // this is where we had an issue, coz node was deleted but link wasn't
          if (!target_node) {
            this.debugErr("Can't transfer data from node " + node.getReadableId() + '. Target node not found');
            continue;
          }

          let target_id = link.target_input_id ?? link.target_slot;
          let target_input = target_node.inputs[target_id];

          if (!target_input) return;
          target_input.data = Utils.parseValue(output.data, target_input.type); // Now change the input of target node, which is connected wit the output

          target_node.isUpdated = true; //now we made the target node also updated as like on setOutputData, so that target node will perform same operation for it's targeted nodes
          target_input.updated = true; // plays important role for onInputUpdated()
        }
      }
    }
  }

  /**
   * Returns the amount of time the container has been running in milliseconds
   * @method getTime
   * @returns number of milliseconds the container has been running
   */
  getTime(): number {
    return this.globaltime;
  }

  /**
   * Sends action to renderer
   * @param action
   * @param params
   */
  sendActionToRenderer(action: string, params?: Array<any>): void {
    if (!this.renderers) return;

    for (let i = 0; i < this.renderers.length; ++i) {
      let c = this.renderers[i];
      if (c[action]) c[action].apply(c, params);
    }
  }

  /**
   * Get nodes
   */
  getNodes(includeSubcontainers = false): Array<Node> {
    let nodes = [];

    addNodes(this._nodes);

    function addNodes(_nodes) {
      for (let n in _nodes) {
        nodes.push(_nodes[n]);

        if (includeSubcontainers && _nodes[n].sub_container) {
          addNodes(_nodes[n].sub_container._nodes);
        }
      }
    }

    return nodes;
  }

  private static setProperties(properties, node) {
    if (properties) {
      for (let i in properties) {
        if (properties.hasOwnProperty(i)) {
          switch (i) {
            case 'inputs':
              break;
            case 'outputs':
              break;
            case 'settings':
              ContainerUtils.addSettingsProperties(node, properties[i]);
              break;
            default:
              node[i] = properties[i];
          }
        }
      }
    }
  }

  private static setSettings(properties, node) {
    if (properties && properties['settings']) {
      ContainerUtils.addSettingsProperties(node, properties['settings']);
    }
  }

  /**
   * Create node and add to container
   * You can define any additional node properties or/and serialized node info.
   * If properties.id && serializedNode.id is not defined, container will create a new instance of node,
   * generate new id, call node.onCreated? store it node in db, and call node.onAdded.
   * If properties.id || serializedNode.id is defined, it will create node, add and call node.onAdded
   * (will not store node in db).
   * @param type type of node
   * @param properties set any additional node properties (pos, size, etc...)
   * @param serializedNode configure node from serialized info
   * @param force forcefully add node on DB, true for operation like import
   * @param callback called when node creation and related database operations have completed
   * @returns {Node}
   */
  createNode(
    type: string,
    properties?: any,
    serializedNode?: SerializedNode,
    force = false,
    callback: (err?: Error) => void = doNothing,
  ): Node {
    if (this.container_node && this.container_node.shouldRejectNodeType(type)) {
      this.debugErr('Trying to create non-compatible child node in container...');
      return null;
    }
    // check class exist
    let node_class = Container.nodes_types[type];
    if (!node_class) {
      this.debugErr("Can't create node. Node class of type [" + type + '] not registered.');
      return null;
    }

    let { id, isNew } = this.getNodeId(properties, serializedNode, type);

    let node: Node = new node_class(this, id, properties);

    //set node properties
    node.id = id;
    node.cid = this.id;
    node.container = this;
    node.side = this.side;
    node.type = type;
    node.category = node_class.category;

    const settings = { ...node.settings };

    // deserialize
    if (serializedNode) node.configure(serializedNode);
    if (isNew || force) {
      if (node['setup']) node['setup']();
    }

    // repopulate validation
    if (!isNew) {
      Object.keys(node.settings).forEach(key => {
        node.settings[key].validation = settings[key] ? settings[key].validation : null;
      });
    }

    // set additional properties
    Container.setProperties(properties, node);
    if (node instanceof ContainerNode && properties && properties.sub_container) {
      node.sub_container = Container.containers[properties.sub_container.id];
      node.settings.name.value =
        (node.settings && node.settings.name && node.settings.name.value) || 'Folder ' + properties.sub_container.id;
    }

    if (!node.title) node.title = node.type;
    if (
      (!properties || !properties.properties || !properties.properties.size) &&
      (!serializedNode || !serializedNode.size) &&
      !force
    ) {
      node.size = node.computeSize(true);
    }
    // overwrite id
    node.id = id;

    if (isNew || force) {
      if (node['init']) node['init'](properties);
      // set settings values after dynamic initialization completion; on above only static settings got filled
      Container.setSettings(properties, node);
      this.createNewNode(node).then(() => {
        callNodeMethodOnAdded();
      });
    } else {
      callNodeMethodOnAdded();
    }

    if (isNew && this.db) {
      if (this.id == 0) this.db.updateLastRootNodeId(this.last_node_id, callback);
      else
        this.db.updateNode(
          this.container_node.id,
          this.container_node.container.id,
          {
            $set: {
              'sub_container.last_node_id': this.container_node.sub_container.last_node_id,
            },
          },
          callback,
        );
    } else setTimeout(callback, 0);

    this.setDirtyCanvas(true, true);
    // add node
    this._nodes[id] = node;
    Registry._nodes[Registry.getId(this.id, id)] = node;
    return node;

    function callNodeMethodOnAdded() {
      if (node['onAdded']) node['onAdded']();
    }
  }

  private getNodeId(properties: any, serializedNode: SerializedNode, type: string) {
    let id;
    let isNew = false;
    if (properties && properties.id != null) id = properties.id;
    else if (serializedNode && serializedNode.id != null) id = serializedNode.id;
    else {
      id = ++this.last_node_id;
      isNew = true;
    }

    // check node id not exist
    if (this._nodes[id]) {
      this.debugErr(` Node id [ ${id} ] already exist.`);
      const lastNodeId = Math.max(...Object.keys(this._nodes).map(x => Number(x)));
      this.last_node_id = lastNodeId + 1;
      id = this.last_node_id;
      this.debugErr(`Forcefully creating node of type [ ${type} ] with id [ ${id} ].`);
      if (this.container_node.cid === 0) {
        this.db.updateLastRootNodeId(this.last_node_id);
      } else {
        ContainerUtils.updateSubContainerLastNodeId(this, this.last_node_id);
      }
    }

    return { id, isNew };
  }

  private createNewNode(node: any): Promise<void> {
    this.debug('New node created: ' + node.getReadableId());
    if (node['onCreated']) node['onCreated']();
    return new Promise<void>(resolve => {
      if (this.db) {
        // Executes only on server side
        this.db.addNode(node, () => {
          resolve();
        });
      } else {
        resolve();
      }
    });
  }

  removeBroadcasted(node: Node) {
    if (this.side === Side.server) {
      this.remove(node);
      // remove from user interface
      this.server_editor_socket.emit('nodes-delete', { nodes: [node.id], cid: node.cid });
    }
  }

  /**
   * Removes a node from the container
   * @param node the instance of the node or node id
   */
  remove(node: Node | number): void {
    // if id provided, get node
    if (typeof node == 'number') {
      node = this._nodes[node];
      if (!node) this.debugErr("Can't remove node. Node id [" + node + ' not exist.');
      return null;
    }
    if (node.ignore_remove) return;
    const callback = get(node, 'onRemoved', () => {});
    Promise.resolve(callback.call(node))
      .then(() => this.afterOnRemoved(<Node>node))
      .catch(err => log.error(err));
  }

  private afterOnRemoved(node: Node) {
    // disconnect inputs
    if (node.inputs)
      for (let i in node.inputs) {
        let input = node.inputs[i];
        if (input.link != null) node.disconnectInputLink(+i);
      }

    // disconnect outputs
    if (node.outputs)
      for (let o in node.outputs) {
        let output = node.outputs[o];
        if (output.links != null && output.links.length > 0) node.disconnectOutputLinks(+o);
      }

    // remove from renderer
    if (this.renderers) {
      for (let i = 0; i < this.renderers.length; ++i) {
        let renderer = this.renderers[i];
        if (renderer.selected_nodes[node.id]) delete renderer.selected_nodes[node.id];
        if (renderer.node_dragged == node) renderer.node_dragged = null;
      }
    }

    // remove from container
    delete this._nodes[node.id];
    delete Registry._nodes[Registry.getId(this.id, node.id)];

    this.debug('Node deleted: ' + node.getReadableId());
    node.container = null;

    if (this.db) this.db.removeNode(node.id, this.id);

    this.setDirtyCanvas(true, true);
  }

  /**
   * Returns a node by its id.
   * @param id
   */
  getNodeById(id: number): Node {
    if (id == null) return null;
    return this._nodes[id];
  }

  /**
   * Returns a list of nodes that matches a type
   * @param type the name of the node type
   * @param includeSubContainers
   * @returns a list with all the nodes of this type
   */
  getNodesByType(type: string, includeSubContainers = false): Array<Node> {
    type = type.toLowerCase();
    let r = [];

    for (let id in this._nodes) {
      let node = this._nodes[id];
      if (node.type.toLowerCase() == type) r.push(node);
    }

    if (includeSubContainers) {
      for (let id in this._nodes) {
        let node = this._nodes[id];
        if (node instanceof ContainerNode) {
          let nodes = (<ContainerNode>node).sub_container.getNodesByType(type, true);
          nodes.forEach(n => r.push(n));
        }
      }
    }

    return r;
  }

  /**
   * Returns a list of nodes that are an instance of the provided node-class (or one of its subclasses)
   * @returns a list with all the nodes of this type
   * @param nodeClass
   * @param includeSubcontainers
   */
  getNodesByClass(nodeClass: Function, includeSubcontainers = false): Array<Node> {
    let r = [];

    for (let id in this._nodes) {
      let node = this._nodes[id];
      if (node instanceof nodeClass) r.push(node);
    }

    if (includeSubcontainers) {
      for (let id in this._nodes) {
        let node = this._nodes[id];
        if (node instanceof ContainerNode) {
          let nodes = (<ContainerNode>node).sub_container.getNodesByClass(nodeClass, true);
          nodes.forEach(n => r.push(n));
        }
      }
    }

    return r;
  }

  getAllNodes(
    parent: { [id: number]: Node } = this._nodes,
    parentId: number = 0,
  ): Array<{
    id: string;
    name: string;
    isContainer: boolean;
    list?: Array<{ id: string; name: string }>;
  }> {
    let list = [];
    Object.keys(parent).forEach(id => {
      const node = parent[id];
      if (node instanceof ContainerNode) {
        list.push({
          id: node.sub_container.id,
          name: node.sub_container.name,
          parentId,
          list: this.getAllNodes(node.sub_container._nodes, node.sub_container.id),
          isContainer: true,
        });
      } else {
        list.push({
          id: node.id,
          parentId,
          name: node.name ? `${node.name}: ${node.title}` : node.title,
          isContainer: false,
        });
      }
    });
    return list.sort((a, b) => {
      if (a.isContainer && b.isContainer) {
        return a.id - b.id;
      } else if (a.isContainer && !b.isContainer) {
        return -1;
      } else return 1;
    });
  }

  /**
   * Set canvas to dirty for update
   * @param foreground
   * @param backgroud
   */
  setDirtyCanvas(foreground?: boolean, backgroud?: boolean): void {
    this.sendActionToRenderer('setDirty', [foreground, backgroud]);
  }

  /**
   * Executes a callback with the editor instance if available
   */
  runWithEditor(f: (Editor) => void) {
    const editor = get(this, 'renderers[0].editor');
    if (editor) f(editor);
  }

  /**
   * Creates a Object containing all the info about this container, it can be serialized
   * @returns value of the node
   */
  serialize(only_dashboard_nodes = false): SerializedContainer {
    let data: SerializedContainer = {
      id: this.id,
      last_node_id: this.last_node_id,
      name: this.name,
      config: this.config,
    };
    let ser_nodes: Array<SerializedNode> = [];
    for (let id in this._nodes) {
      const node = this._nodes[id];
      if (only_dashboard_nodes && !node.isDashboardNode) continue;
      ser_nodes.push(node.serialize());
    }
    data.serializedNodes = ser_nodes;
    if (this.id == 0) (<any>data).last_container_id = Container.last_container_id;
    return data;
  }

  /**
   * Add nodes_list to container from a JSON string
   * @param data JSON string
   * @param keep_old
   */
  configure(data: SerializedContainer, keep_old = false): boolean {
    if (!keep_old) this.clear();

    //copy all fields to this container
    for (let i in data) {
      //skip nodes
      if (i == 'serializedNodes') continue;

      this[i] = data[i];
    }

    let error = false;

    if (data.serializedNodes) {
      for (let n of data.serializedNodes) {
        let node = this.createNode(n.type, null, n);
        if (!node) error = true;
      }
    }

    if ((<any>data).last_container_id) Container.last_container_id = (<any>data).last_container_id;

    this.setDirtyCanvas(true, true);
    return error;
  }
  /**
   * Print debug message to console
   * @param message
   */
  debug(message: string): void {
    log.debug('[' + this.id + '] ' + message);
  }

  /**
   * Print error message to console
   * @param message
   * @param module
   */
  debugWarn(message: string, module?: string): void {
    log.warn('[' + this.id + '] ' + message);
  }

  /**
   * Print error message to console
   * @param message
   * @param module
   */
  debugErr(message: string, module?: string): void {
    log.error('[' + this.id + '] ' + message);
  }
}
