import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathLogarithmNode extends Node {
  constructor() {
    super();

    this.title = 'Logarithm (Base 10)';
    this.description = "'log10(x)' is the result of log('x') with base 10.";

    this.addInput('x', Type.NUMBER);
    this.addOutput('log10(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.log10(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/log10', MathLogarithmNode);
