import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathModulusNode extends Node {

  constructor() {
    super();

    this.title = 'Modulus';
    this.description =
      "('output' = 'in 1' % 'in 2') 'output' is the modulus of the Numeric inputs. Input values can be set from settings.";
    this.addInputWithSettings('in 1', Type.NUMBER, 0, 'in 1');
    this.addInputWithSettings('in 2', Type.NUMBER, 1, 'in 2');
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const a = this.getInputData(0);
    const b = this.getInputData(1);

    if (a != null && b != null) this.setOutputData(0, a % b);
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math/modulus', MathModulusNode);
