import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';

//Numeric Constant
class NumConstantNode extends Node {

  constructor() {
    super();
    this.title = 'Numeric Constant';
    this.description =
      'Outputs a Numeric value set from settings. Can also output null by leaving the settings field blank.';

    this.settings['value'] = { description: 'Value', value: null, type: SettingType.NUMBER };
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onAfterSettingsChange();
  }

  onExecute(){
    // this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    const val = this.settings['value'].value;
    this.setOutputData(0, Utils.parseValue(val, Type.NUMBER));
  }
}
Container.registerNodeType('point/num-constant', NumConstantNode);
