import { Node, SettingType } from '../../node';
import { Container, Side } from '../../container';
import Icons from '../../node-icons';
import { Type } from '../../node-io';
import { isNull } from '../../../utils/helper';

const icon = Icons.aiIcon;

export class ConnectionLocalReceiverNode extends Node {
  topicList: any = [];

  constructor() {
    super();
    this.title = 'Link Receiver Topic';
    this.description =
      "This node works in conjunction with link-transmitter node and provides a connection of nodes without the graphical wires. 'out #' outputs will match the corresponding 'in #' input from link-transmitter nodes with matching 'Channel Number' settings.  The number of outputs is configurable from settings.";
    this.iconImageUrl = icon;
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addOutput('output', Type.ANY);
    this.addOutput('topic-id', Type.STRING);
    this.settings['channel'] = {
      description: 'Topic Name',
      config: { items: [] },
      value: '',
      type: SettingType.DROPDOWN,
    };
    this.settings['blockNull'] = {
      description: 'Block Null',
      value: false,
      type: SettingType.BOOLEAN,
    };
  }

  init() {
    if (!this.properties['val']) {
      this.properties['val'] = null;
    }
  }

  onAdded() {
    this.onAfterSettingsChange();
    this.setOutputData(0, this.properties['val']);
  }

  onCreated() {
    if (isNull(this.settings['channel'].value)) {
      this.onAfterSettingsChange();
    }
  }

  onAfterSettingsChange() {
    this.updateTitle();
    if (this.side !== Side.server) return;
    this.transmitterTopic();
    this.updateChannel();
  }

  pushSettings() {
    const unitCategory = this.topicList.map((point: any) => {
      return { value: point, text: point };
    });
    this.settings['channel'].config = {
      items: unitCategory,
    };
    this.persistProperties(true);
    this.updateTitle();
    this.updateChannel();
    this.broadcastSettingsToClients(false);
    this.broadcastTitleToClients(false);
    this.broadcastOutputsToClients(false);
  }

  transmitterTopic() {
    const transmitters = Container.containers[0].getNodesByType('connection/link-transmitter-single', true);
    this.topicList = [];
    transmitters.forEach(transmitter => {
      this.topicList.push(transmitter.getInputData(1));
      if (this.settings['channel'].value === transmitter.getInputData(1)) {
        this.setOutputData(0, transmitter.getInputData(0));
      }
    });
    this.pushSettings();
  }

  updateTitle() {
    this.title = 'Link Receiver Single [' + this.settings['channel'].value + ']';
  }

  updateChannel() {
    this.setOutputData(1, this.settings['channel'].value);
  }

  // @Link: Triggers from transmitter on addition and on remove
  triggerTransmitterTopicUpdate() {
    if (this.side !== Side.server) return;
    this.transmitterTopic();
  }
}
Container.registerNodeType('connection/link-receiver-single', ConnectionLocalReceiverNode);
