import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import TimeUtils from '../../utils/time-utils';

export class TriggeredInjectNode extends Node {
  lastTrigger;

  constructor() {
    super();
    this.title = 'Inject';
    this.description =
      "While 'enable' is 'true', ‘output’ will send 'message' value when 'trigger' transitions from 'false' to 'true";

    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInputWithSettings('message', Type.STRING, 'true', 'Message');
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
  }

  onCreated() {
    this.lastTrigger = false;
  }

  onAdded() {
    const enable = this.getInputData(0);
    if (!enable) {
      this.setOutputData(0, null);
      return;
    }
  }

  onInputUpdated() {
    const enable = this.getInputData(0);
    if (!enable) {
      this.setOutputData(0, null, true);
      return;
    }
    let trigger = this.getInputData(2);
    if (trigger && !this.lastTrigger) {
      const message = this.getInputData(1);
      this.setOutputData(0, message, false);
    }
    this.lastTrigger = trigger;
  }

  onAfterSettingsChange() {}
}
Container.registerNodeType('trigger/inject', TriggeredInjectNode);
