import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';

class ScaleNode extends Node {
  lastTime: number;
  interval: number = 250;

  constructor() {
    super();
    this.title = 'Scale';
    this.description =
      "Performs a linear interpolation of the 'input' value based configured settings.  When 'input' value is between 'in-min' and 'in-max' it is linearly interpolated between 'out-min' and 'out-max'.  If 'input' is greater than the 'in-max' value, 'output' will be the 'out-max' value.  If 'input' is less than the 'in-min' value, 'output' will be the 'out-min' value.  'in-max', 'in-min', 'out-max', and 'out-min' values can be configured from settings.";

    this.addInput('input', Type.NUMBER);
    this.addInputWithSettings('in-min', Type.NUMBER, 0, 'Minimum Input', false);
    this.addInputWithSettings('in-max', Type.NUMBER, 100, 'Maximum Input', false);
    this.addInputWithSettings('out-min', Type.NUMBER, 0, 'Minimum Output', false);
    this.addInputWithSettings('out-max', Type.NUMBER, 10, 'Maximum Output', false);
    this.addOutput('output', Type.NUMBER);
    this.lastTime = Date.now();
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onAfterSettingsChange(){
    this.onInputUpdated();
  }

  onInputUpdated() {
    let now = Date.now();
    if (now - this.lastTime >= this.interval) {
      this.lastTime = now;
      let val = this.getInputData(0);
      const inMin = this.getInputData(1);
      const inMax = this.getInputData(2);
      const outMin = this.getInputData(3);
      const outMax = this.getInputData(4);

      if (val == null) {
        this.setOutputData(0, 0);
      } else {
        val = Utils.remap(val, inMin, inMax, outMin, outMax);
        this.setOutputData(0, val.toFixed(1));
      }
    }
  }
}
Container.registerNodeType('num-transform/scale', ScaleNode);
