import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';

class OrNode extends FlexibleNode {

  constructor() {
    super();
    this.dynamicSettingsExist = false;
    this.dynamicInputsType = Type.BOOLEAN;
    this.title = 'OR';
    this.description =
      "Performs a logical 'OR' operation (output 'true' when ANY input is 'true'). You can specify the number of inputs in the node settings.";
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('out not', Type.BOOLEAN);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const allInputs = this.getDefinedInputValues();
    if (allInputs.length == 0) {
      this.setOutputData(0, false);
      this.setOutputData(1, true);
      return;
    }

    const anyTrue = allInputs.some(v => v == true);
    this.setOutputData(0, anyTrue);
    this.setOutputData(1, !anyTrue);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('boolean/or', OrNode);
