import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class TextASCIICharNode extends Node {
  constructor() {
    super();

    this.title = 'ASCII Code to Char';
    this.description = "'output' is the character of the given Numeric ASCII code 'input' value.";

    this.addInput('input', Type.NUMBER);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (val != null) this.setOutputData(0, String.fromCharCode(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('string/ascii-to-char', TextASCIICharNode);
