import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathAsinNode extends Node {
  constructor() {
    super();

    this.title = 'Asin';
    this.description =
      "'asin(x)' is the result of arcsin('x').  The Inverse Sine function of input 'x'.";
    this.addInput('x', Type.NUMBER);
    this.addOutput('asin(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (val != null) this.setOutputData(0, Math.asin(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/asin', MathAsinNode);
