import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';

class NumSwitchNode extends Node {

  constructor() {
    super();
    this.title = 'Numeric Switch';
    this.description =
      "Boolean 'switch' passes corresponding Numeric input to 'output'.  If 'switch' is 'true', Numeric 'inTrue' value will be passed to 'output'  'inTrue' and 'inFalse' values can be set in settings.";

    this.addInput('switch', Type.BOOLEAN);
    this.addInputWithSettings('inTrue', Type.NUMBER, null, 'True Value');
    this.addInputWithSettings('inFalse', Type.NUMBER, null, 'False Value');
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let select = this.getInputData(0);
    if (!Utils.hasInput(select)) {
      this.setOutputData(0, null);
      return;
    }
    const inA = this.getInputData(1);
    const inB = this.getInputData(2);
    if (select) this.setOutputData(0, inA);
    else this.setOutputData(0, inB);
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('switch/num-switch', NumSwitchNode);
