import * as NeDBDataStore from 'nedb';

import UserDatabase from '../database/UserDatabase';
import { User } from '../models/user';
import config from '../../config';

const log = require('logplease').create('database', { color: 4 });

export default class UserDataSource implements UserDatabase {
  private users: NeDBDataStore;

  async asyncLoadDatabase(cleanUpInterval: any): Promise<any> {
    const path = config.dataDir;
    this.users = new NeDBDataStore(`${path}/users.db`);
    this.users.persistence.setAutocompactionInterval(cleanUpInterval);
    return this.loadUsers();
  }

  private loadUsers(): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.users.loadDatabase(err => {
        if (err) {
          log.error(err);
          reject(err);
        }
        return resolve();
      });
    });
  }

  addUser(user: User, callback?: (err?: Error, doc?: User) => void) {
    this.users.insert(user, function(err, doc) {
      if (err) log.error(err);
      if (callback) callback(err, doc);
    });
  }

  getUser(query: object, callback?: (err?: Error, doc?: User) => void) {
    this.users.findOne(query, function(err, doc: any) {
      if (err) log.error(err);
      if (callback) callback(err, doc);
    });
  }

  getUsers(callback?: (err?: Error, docs?: Array<User>) => void) {
    this.users.find({}, function(err, docs) {
      if (err) log.error(err);
      if (callback) callback(err, docs);
    });
  }

  getAsyncUsers(): Promise<any> {
    return new Promise((resolve, reject) => {
      this.users.find({}, function(err, doc: any) {
        if (err) {
          log.error(err);
          reject(err);
        } else resolve(doc);
      });
    });
  }

  updateUser(query: object, user: User, callback?: (err?: Error) => void) {
    this.users.update(query, user, {}, function(err, updated) {
      if (err) log.error(err);
      if (updated == 0) log.error(`Cat't update user [${name}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  getUsersCount(callback?: (err?: Error, num?: number) => void) {
    this.users.count({}, function(err, num) {
      if (err) log.error(err);
      if (callback) callback(err, num);
    });
  }

  addOrUpdateUser(query: object, user: User): Promise<any> {
    return new Promise((resolve, reject) => {
      this.getUser(query, (err, usr) => {
        if (usr) {
          this.updateUser(query, user, err => {
            if (err) reject(err);
            else resolve();
          });
        } else {
          this.addUser(user, err => {
            if (err) reject(err);
            else resolve();
          });
        }
      });
    });
  }

  removeUser(query: object, callback?: (err?: Error) => void) {
    this.users.remove(query, {}, function(err, removed) {
      if (err) log.error(err);
      if (callback) callback(err);
    });
  }

  dropUsers(callback?: (err?: Error) => void) {
    this.users.remove({}, { multi: true }, function(err) {
      if (err) log.error(err);
      if (callback) callback(err);
    });
  }
}
