import { Node, SerializedNode } from '../nodes/node';
import { UiPanel } from '../server/dashboard';
import { Database } from './database';

import { User } from './models/user';
import { Schedule } from './models/schedule';

import { History } from './models/historian'; //

import AppDataSource from './datasource/AppDataSource';
import DashboardDataSource from './datasource/DashboardDataSource';
import NodeDataSource from './datasource/NodeDataSource';
import ScheduleDataSource from './datasource/ScheduleDataSource';
import HistorianDataSource from './datasource/HistorianDataSource'; //
import UserDataSource from './datasource/UserDataSource';

import AppDatabase from './database/AppDatabase';
import DashboardDatabase from './database/DashboardDatabase';
import NodeDatabase from './database/NodeDatabase';
import ScheduleDatabase from './database/ScheduleDatabase';
import HistorianDatabase from './database/HistorianDatabase'; //
import UserDatabase from './database/UserDatabase';
import AuthUserDatabase from './database/AuthUserDatabase';
import { AuthUser } from './models/AuthUser';
import AuthUserDataSource from './datasource/AuthUserDataSource';

class NeDbDatabase implements Database {
  private static CLEANUP_INTERVAL = 5 * 60 * 1000;
  private authUserDatabase: AuthUserDatabase;
  private userDatabase: UserDatabase;
  private nodeDatabase: NodeDatabase;
  private dashboardDatabase: DashboardDatabase;
  private appDatabase: AppDatabase;
  private scheduleDatabase: ScheduleDatabase;
  private historianDatabase: HistorianDatabase; //

  constructor() {
    this.authUserDatabase = new AuthUserDataSource();
    this.userDatabase = new UserDataSource();
    this.nodeDatabase = new NodeDataSource();
    this.dashboardDatabase = new DashboardDataSource();
    this.appDatabase = new AppDataSource();
    this.scheduleDatabase = new ScheduleDataSource();
    this.historianDatabase = new HistorianDataSource(); //
  }

  async asyncLoadDatabase(cleanUpInterval = NeDbDatabase.CLEANUP_INTERVAL): Promise<any> {
    try {
      await this.authUserDatabase.asyncLoadDatabase(cleanUpInterval);
      await this.userDatabase.asyncLoadDatabase(cleanUpInterval);
      await this.nodeDatabase.asyncLoadDatabase(cleanUpInterval);
      await this.dashboardDatabase.asyncLoadDatabase(cleanUpInterval);
      await this.appDatabase.asyncLoadDatabase(cleanUpInterval);
      await this.scheduleDatabase.asyncLoadDatabase(cleanUpInterval);
      await this.historianDatabase.asyncLoadDatabase(cleanUpInterval); //
    } catch (e) {
      throw e;
    }
    return null;
  }

  addUser(user: User, callback?: (err?: Error, doc?: User) => void) {
    this.userDatabase.addUser(user, callback);
  }

  addOrUpdateUser(query: object, user: User): Promise<any> {
    return this.userDatabase.addOrUpdateUser(query, user);
  }

  getUsers(callback?: (err?: Error, docs?: Array<User>) => void) {
    this.userDatabase.getUsers(callback);
  }

  getUser(query: object, callback?: (err?: Error, doc?: User) => void) {
    this.userDatabase.getUser(query, callback);
  }

  getAsyncUsers(): Promise<any> {
    return this.userDatabase.getAsyncUsers();
  }

  updateUser(query: object, user: User, callback?: (err?: Error) => void) {
    this.userDatabase.updateUser(query, user);
  }

  getUsersCount(callback?: (err?: Error, num?: number) => void) {
    this.userDatabase.getUsersCount(callback);
  }

  removeUser(query: object, callback?: (err?: Error) => void) {
    this.userDatabase.removeUser(query, callback);
  }

  dropUsers(callback?: (err?: Error) => void) {
    this.userDatabase.dropUsers();
  }

  addNode(node: Node, callback?: (err?: Error, doc?: SerializedNode) => void) {
    this.nodeDatabase.addNode(node, callback);
  }

  getNodes(callback?: (err?: Error, docs?: Array<SerializedNode>) => void) {
    this.nodeDatabase.getNodes(callback);
  }

  getNodeType(type: string, callback?: (err?: Error, docs?: Array<SerializedNode>) => void) {
    this.nodeDatabase.getNodeType(type, callback);
  }

  getNodeByQuery(query, callback?: (err?: Error, docs?: Array<any>) => void) {
    this.nodeDatabase.getNodeByQuery(query, callback);
  }

  getNode(id: number, cid: number, callback?: (err?: Error, doc?: SerializedNode) => void) {
    this.nodeDatabase.getNode(id, cid, callback);
  }

  updateNode(id: number, cid: number, update: any, callback?: (err?: Error) => void) {
    this.nodeDatabase.updateNode(id, cid, update, callback);
  }

  removeNode(id: number, cid: number, callback?: (err?: Error) => void) {
    this.nodeDatabase.removeNode(id, cid, callback);
  }

  dropNodes(callback?: (err?: Error) => void) {
    this.nodeDatabase.dropNodes(callback);
  }

  addUiPanel(panel: UiPanel, callback?: (err?: Error, doc?: UiPanel) => void) {
    this.dashboardDatabase.addUiPanel(panel, callback);
  }

  getUiPanels(callback?: (err?: Error, docs?: UiPanel[]) => void) {
    this.dashboardDatabase.getUiPanels(callback);
  }

  getUiPanel(name: string, callback?: (err?: Error, doc?: UiPanel) => void) {
    this.dashboardDatabase.getUiPanel(name, callback);
  }

  updateUiPanel(name: string, update: any, callback?: (err?: Error) => void) {
    this.dashboardDatabase.updateUiPanel(name, update, callback);
  }

  dropUiPanels(callback?: (err?: Error) => void) {
    this.dashboardDatabase.dropUiPanels(callback);
  }

  removeUiPanel(name: string, callback?: (err?: Error) => void) {
    this.dashboardDatabase.removeUiPanel(name, callback);
  }

  getLastContainerId(callback?: (err?: Error, id?: number) => void) {
    this.appDatabase.getLastContainerId(callback);
  }

  updateLastContainerId(id: number, callback?: (err?: Error) => void) {
    this.appDatabase.updateLastContainerId(id, callback);
  }

  getLastRootNodeId(callback?: (err?: Error, id?: number) => void) {
    this.appDatabase.getLastRootNodeId(callback);
  }

  updateLastRootNodeId(id: number, callback?: (err?: Error) => void) {
    this.appDatabase.updateLastRootNodeId(id, callback);
  }

  dropApp(callback?: (err?: Error) => void) {
    this.appDatabase.dropApp(callback);
  }

  addSchedule(schedule: Schedule, callback?: (err?: Error, doc?: Schedule) => void) {
    this.scheduleDatabase.addSchedule(schedule, callback);
  }

  updateSchedule(query: object, schedule: Schedule, callback?: (err?: Error) => void) {
    this.scheduleDatabase.updateSchedule(query, schedule, callback);
  }

  removeSchedule(query: object, callback?: (err?: Error) => void) {
    this.scheduleDatabase.removeSchedule(query, callback);
  }

  //// HistorianDatabase historian history

  addHistory(history: History, callback?: (err?: Error, doc?: History) => void) {
    this.historianDatabase.addHistory(history, callback);
  }

  updateHistory(query: object, history: History, callback?: (err?: Error) => void) {
    this.historianDatabase.updateHistory(query, history, callback);
  }

  removeHistory(query: object, callback?: (err?: Error) => void) {
    this.historianDatabase.removeHistory(query, callback);
  }

  addAuthUser(authUser: AuthUser): Promise<AuthUser> {
    return this.authUserDatabase.addAuthUser(authUser);
  }

  getAuthUser(): Promise<AuthUser> {
    return this.authUserDatabase.getAuthUser();
  }

  deleteAuthUsers(): Promise<string> {
    return this.authUserDatabase.deleteAuthUsers();
  }

  getAuthToken(authUser: AuthUser): Promise<string> {
    return this.authUserDatabase.getAuthToken(authUser);
  }

  isAuthenticated(token: string): Promise<any> {
    return this.authUserDatabase.isAuthenticated(token);
  }


}

const db = new NeDbDatabase();
export { db };
