import * as NeDBDataStore from 'nedb';

import ScheduleDatabase from '../database/ScheduleDatabase';
import { Schedule } from '../models/schedule';
import config from '../../config';

const log = require('logplease').create('database', { color: 4 });

export default class ScheduleDataSource implements ScheduleDatabase {
  private schedule: NeDBDataStore;

  async asyncLoadDatabase(cleanUpInterval: any): Promise<any> {
    const path = config.dataDir;
    this.schedule = new NeDBDataStore(`${path}/schedule.db`);
    this.schedule.persistence.setAutocompactionInterval(cleanUpInterval);
    return this.loadSchedule();
  }

  private loadSchedule(): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.schedule.loadDatabase(err => {
        if (err) {
          log.error(err);
          reject(err);
        }
        return resolve();
      });
    });
  }

  addSchedule(schedule: Schedule, callback?: (err?: Error, doc?: Schedule) => void) {
    this.schedule.insert(schedule, function(err, doc) {
      if (err) log.error(err);
      if (callback) callback(err, doc);
    });
  }

  updateSchedule(
    query: object,
    schedule: Schedule,
    callback?: (err?: Error) => void,
    updatedCallback?: (updated?: number) => void,
  ) {
    this.schedule.update(query, schedule, {}, function(err, updated) {
      if (err) log.error(err);
      else {
        updatedCallback(updated);
      }
      if (updated == 0) log.error(`Can't update schedule [${name}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  //remove
  removeSchedule(
    query: object,
    callback?: (err?: Error) => void,
    removedCallback?: (removed?: number) => void,
  ) {
    this.schedule.remove(query, {}, function(err, removed) {
      if (err) {
        log.error(err);
      } else {
        removedCallback(removed);
      }
      if (removed == 0) log.debug("Cat't remove. Schedule Document not found.");
      if (callback) callback(err);
    });
  }
}
