import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathAtanNode extends Node {
  constructor() {
    super();

    this.title = 'Atan';
    this.description =
      "'atan(x)' is the result of arctan('x').  The Inverse Tangent function of input 'x'.";

    this.addInput('x', Type.NUMBER);
    this.addOutput('atan(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (val != null) this.setOutputData(0, Math.atan(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/atan', MathAtanNode);
