
// THIS NODE IS A COPY OF THE STREAM-MERGE NODE.  
// IT SHOULD BE REMOVED ONCE NODES ARE DUPLICATED TO MULTIPLE CATEGORIES

import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import { FlexibleNode } from '../../flexible-node';

class HubNode extends FlexibleNode {
  val: number;
  lastReset: boolean;

  constructor() {
    super();
    this.dynamicSettingsExist = false;
    this.dynamicInputsType = Type.ANY;
    this.title = 'Hub';
    this.description =
      "This node merges multiple inputs into a single stream/output.  Any new 'input' ('in #') value will be passed to 'output'.  The number of inputs can be modified from settings.";
    this.addOutput('output', Type.ANY);
  }

  onAdded() {
    this.onInputUpdated();
    this.size = this.computeSize();
  }

  onInputUpdated() {
    const inputs = this.getInputsCount();
    for (let i = this.dynamicInputStartPosition; i < inputs; i++) {
      let inputValue = this.getInputData(i);
      if (Utils.hasInput(inputValue)) {
        if (this.inputs[i].updated) {
          this.setOutputData(0, this.inputs[i].data);
          return;
        }
      }
    }
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('connection/hub', HubNode);
