import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathAcosNode extends Node {
  constructor() {
    super();

    this.title = 'Acos';
    this.description =
      "'acos(x)' is the result of arccos('x').  The Inverse Cosine function of input 'x'.";
    this.addInput('x', Type.NUMBER);
    this.addOutput('acos(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (val != null) this.setOutputData(0, Math.acos(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/acos', MathAcosNode);
