import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathExpNode extends Node {
  constructor() {
    super();

    this.title = 'Exp';
    this.description =
      "('exp(x)' = E^'x') 'exp(x)' is the result of raising E (Euler's Number 2.71828) to the power of 'x'.  The Natural Exponential function.";

    this.addInput('x', Type.NUMBER);
    this.addOutput('exp(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.exp(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/exp', MathExpNode);
