import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class ToggleNode extends Node {
  state = false;
  lastVal;
  count = 0;


  constructor() {
    super();

    this.title = 'Toggle';
    this.description =
      "When the 'input' transitions from 'false' to 'true', 'output' will switch between 'false'/'true'.   (See Figure A.)";
    this.addInput('input', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    //this.addOutput('toggle out', Type.BOOLEAN);
    this.settings['reset-on-disc'] = {
      description: 'Reset on disconnected or input is null',
      value: false,
      type: SettingType.BOOLEAN,
    };
    this.setOutputData(0, null);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let input = this.getInputData(0);

    // sends a true/false on each input update
    if (this.count === 0) {
      //this.setOutputData(1, false);
      this.count = 1;
    } else if (this.count === 1) {
      //this.setOutputData(1, true);
      this.count = 0;
    }

    if (input == null || input == undefined) {
      if (this.settings['reset-on-disc'].value) {
        this.lastVal = false;
        this.setOutputData(0, null);
        this.state = false;
      }
    } else if (input == this.lastVal) return;
    if (input == true) {
      this.setOutputData(0, !this.state);
      this.state = !this.state;
    }
    this.lastVal = input;
  }
}
Container.registerNodeType('boolean/toggle', ToggleNode);
