import { Container, Side } from '../../container';
import { Type } from '../../node-io';
import { UiNode } from './ui-node';

export class UiTextBoxNode extends UiNode {
  constructor(container?: Container, id?: number, properties?: any) {
    super('TextBox', 'UiTextBoxNode', properties);

    this.description =
      "Dashboard node which provides a user input text box on the dashboard.  The entered string is sent from the node 'output'.  The text-box can be overridden by wiring a String into the node 'input'. ";
    this.title = 'Text-Box';
    this.setState('');

    this.addInput('input', Type.STRING);
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
    super.onAdded();
    if (this.side == Side.server) this.setOutputData(0, this.getState());
  }

  onGetMessageToServerSide(data) {
    this.setValue(data.state);
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    this.setValue(val);
  }

  setValue(val) {
    this.setOutputData(0, val);
    this.sendIOValuesToEditor();
    this.isRecentlyActive = true;

    if (this.getState() != val)
      //prevent loop sending
      this.setState(val);
  }
}

Container.registerNodeType('dashboard/text-box', UiTextBoxNode);
