import { isNull } from '../../../utils/helper';
import { Container } from '../../container';
import { Node} from '../../node';
import { NodeIO, Type } from '../../node-io';
import Registry from '../../registry';
import { AbstractFolderIONode } from './abstract-folder-io';

class ContainerInputNode extends AbstractFolderIONode {
  constructor(container) {
    super(container, 'Folder Input', 'Input of the container');
    this.addOutput('input', null);
  }

  onCreated() {
    if (!this.isUnderContainer()) return;
    let containerNode = this.container.container_node;
    const id = containerNode.addInput(this.name, Type.ANY, undefined, undefined, { subNodeId: this.id });
    this.name = containerNode.inputs[id].name;
    this.linkHandler.recomputeInputLinks(Registry.getId(containerNode.cid, containerNode.id));
  }

  onRemoved() {
    if (!this.isUnderContainer()) return;
    let containerNode = this.container.container_node;
    let [inputId, _] = this.findParentOutputById(this.container.container_node, 'inputs');
    if (isNull(inputId)) {
      return;
    }
    containerNode.removeInput(+inputId);
    this.linkHandler.recomputeInputLinks(Registry.getId(containerNode.cid, containerNode.id));
    this.emitChange(containerNode, 'size');
  }

  onAfterSettingsChange() {
    if (!this.isUnderContainer()) return;
    const containerNode = this.container.container_node;
    let [inputId, _] = this.findParentOutputById(this.container.container_node, 'inputs');
    if (isNull(inputId)) {
      return;
    }
    containerNode.inputs[inputId].name = this.name;
    this.emitChange(containerNode, 'inputs');
  }

  onExecute() {
    if (!this.isUnderContainer()) return;
    let [_, i] = this.findParentOutputById(this.container.container_node, 'inputs');
    let input = <NodeIO>i;
    if (input?.updated) this.setOutputData(0, input.data);
  }

  protected correctParentIO(containerNode: Node, slot: number) {
    let input = containerNode.inputs[slot];
    if (isNull(input)) {
      return;
    }
    input.subNodeId = this.id;
    this.emitChange(containerNode, 'inputs');
  }

}

Container.registerNodeType('container/folder-input', ContainerInputNode, null, true, false, true);
