import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class TextSplitStringsNode extends Node {
  currentIndex = 0;
  lastStart:boolean;
  lastNext:boolean;

  constructor() {
    super();

    this.title = 'String Split';
    this.description =
      "Portions of the String 'input' are sent to the String 'output' sequentially when 'next' trasitions from 'false' to 'true'; these 'output' strings are portions of the 'input' string split by the String 'separator' value.  The Numeric 'left' outputs the number of 'output' strings remaining to be sent.  When 'start' transitions from 'false' to 'true', the 'output' string will go back to the first portion of the 'input' string. ";

    this.addInput('input', Type.STRING);
    this.addInputWithSettings('separator', Type.STRING, '', 'Separator Value', false);
    this.addInput('start', Type.BOOLEAN);
    this.addInput('next', Type.BOOLEAN);
    this.addOutput('output', Type.STRING);
    this.addOutput('left', Type.NUMBER);
    this.setOutputData(0, null);
    this.setOutputData(1, 0);
  }

  onCreated() {
    this.lastStart = false;
    this.lastNext = false;
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const start = this.getInputData(2) || false;
    const next = this.getInputData(3) || false;

    if (this.inputs[2].updated && start && !this.lastStart) {
      this.currentIndex = 0;
      this.splitNext();
    }
    this.lastStart = start;

    if (this.inputs[3].updated && next && !this.lastNext) this.splitNext();
    this.lastNext = next;
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }

  splitNext() {
    const text = this.getInputData(0);
    const separator = this.getInputData(1);
    if (text == null || separator == null) return;

    const splittedText = text.split(separator);
    if (this.currentIndex >= splittedText.length) return;

    this.setOutputData(0, splittedText[this.currentIndex]);
    this.setOutputData(1, splittedText.length - this.currentIndex - 1);

    this.currentIndex++;
  }
}
Container.registerNodeType('string/string-split', TextSplitStringsNode);
