export const isNull = (value: any): boolean => value == null; // undefined or null

export const isNotNull = (value: any): boolean => !isNull(value);

export const isJSON = (value: any): boolean => isNotNull(value) && (typeof value === 'object' || Array.isArray(value));

export const isString = (value: any): boolean => {
  if (typeof value === 'string' || value instanceof String) {
    return true
  } else return false
}

export const isEmpty = (value: any): boolean => {
  if (isNull(value)) {
    return true;
  }
  if (value instanceof Array) {
    return value.length === 0;
  }
  if (value instanceof Object) {
    return Object.keys(value).length === 0;
  }
  if (value instanceof String) {
    return value.trim() === '';
  }
  return value.toString().trim() === '';
};

export const toLowerSnakeCase = (label: string): string => {
  return isNull(label)
    ? null
    : label
      .toString()
      .toLowerCase()
      .replace(/-+\s+/g, '_');
};

export const isFunction = (func: any): boolean => func && {}.toString.call(func) === '[object Function]';

export const isNumeric = (n): boolean => !isNaN(parseFloat(n)) && isFinite(n);

export const convertToNumber = (value: any, strict: boolean = false): number => {
  if (isNull(value)) {
    return null;
  }
  let inString = value.toString();
  let n = +inString;
  if (isNumeric(n) && !/^\s*$/.test(inString)) {
    return n;
  }
  if (strict) {
    throw new Error('Invalid number');
  }
  return null;
};

export const Generator = {
  map: (generator, mapper: Function) =>
    function* (...args) {
      for (const x of generator(...args)) yield mapper(x);
    },
  filter: (generator, filter: Function) =>
    function* (...args) {
      for (const x of generator(...args)) if (filter(x)) yield x;
    },
};

export const range = function* (from: number, to: number, step: number = 1) {
  let i = 0,
    length = ~~((to - from) / step) + 1;
  while (i < length) yield from + i++ * step;
};

export const union = (...collections): Set<any> => collections.reduce((c, l) => new Set([...c, ...l]), new Set());

export const unionToArray = (...collections): Array<any> => [...union(...collections)];
