import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';

//String
class StringConstantNode extends Node {

  constructor() {
    super();
    this.title = 'String Constant';
    this.description =
      'Outputs a String value set from settings. Can also output null by leaving the settings field blank.';

    this.settings['value'] = { description: 'Value', value: '', type: SettingType.STRING };
    this.addOutput('output', Type.STRING);
  }

  onAdded() {
 
    this.onAfterSettingsChange();
  }

  onExecute(){
    // this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    const val = this.settings['value'].value;
    this.setOutputData(0, Utils.parseValue(val, Type.STRING));
  }
}
Container.registerNodeType('point/string-constant', StringConstantNode);
