import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import TimezoneUtils from '../../utils/timezone-utils';
import * as moment from 'moment-timezone';
export class TimezoneConvertNode extends Node {
  conversionFunction;
  constructor() {
    super();
    this.title = 'Timezone Conversion';
    this.description =
      `## Description\n ` +
      ` The is node is used to convert two timestamps\n `

    this.addInput('input', Type.ANY);
    this.addOutput('out-time-converted', Type.ANY);
    this.addOutput('out-local-converted', Type.ANY);

    this.settings['region'] = {
      description: 'Select Region',
      type: SettingType.DROPDOWN,
      config: {
        items: TimezoneUtils.tsCategory,
      },
    };
    this.settings['timezone'] = {
      description: 'Select TimeZone (Hit save to refresh)',
      value: "Sydney/Australia",
      type: SettingType.DROPDOWN,
    };

  }
  onAdded() {
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    const input = this.getInputData(0);
    try {
      const m = moment.tz(input, this.settings['timezone'].value);
      const local = moment(input);
      this.setOutputData(0, m.format());
      this.setOutputData(1, local.format());
    } catch (e) {
      this.debugWarn(`ERROR: trying to convert a timezone${e}`);
    }

  }

  onAfterSettingsChange() {
    const region = this.settings['region'].value;
    this.settings['timezone'].config = {
      items: TimezoneUtils.tsType(region),
    };
    this.broadcastSettingsToClients();
    this.onInputUpdated()

  }



}
Container.registerNodeType('time/timezone-conversion', TimezoneConvertNode);
