import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';

class SumNode extends Statistics {
  constructor() {
    super();
    this.title = 'Sum';
    this.description =
      'Outputs the Sum of all the (non null) Numeric inputs. The number of inputs and their values can be modified from settings.';
    this.addOutput('sum', Type.NUMBER);
  }

  onInputUpdated() {
    const data = this.getDefinedInputsOrSettingsValues();
    const sum = Utils.stat.sum(data);
    this.setOutputData(0, sum);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('statistics/sum', SumNode);
