import { SettingType } from './node';
import { Link } from './node-link';

export enum Type {
  STRING   = 'string',
  NUMBER   = 'number',
  BOOLEAN  = 'boolean',
  ANY      = 'any',
  JSON     = 'json',
  DROPDOWN = 'dropdown', // it is same as `ANY`, but when it has setting as pair it checks for validity
}

export interface NodeIO {
  name: string;
  type: Type;
  label?: string;
  locked?: boolean;
  pos?: number;
  round?: boolean;
  data?: any;
  updated?: boolean;
  subNodeId?: number;
}

export class NodeOutput implements NodeIO {
  name: string;
  type: Type;
  links?: Array<Link>;
  label?: string;
  locked?: boolean;
  pos?: number;
  round?: boolean;
  data?: any;
  updated?: boolean;
  subNodeId?: number;
}

export interface InputSetting {
  exist: boolean; // bind setting exist or not
  nullable: boolean; // whether null value is acceptable or not
  hidden: boolean;
}

export class NodeInput implements NodeIO {
  name: string;
  type: Type;
  link?: Link;
  label?: string;
  locked?: boolean;
  pos?: number;
  round?: boolean;
  isOptional?: boolean;
  data?: any;
  updated?: boolean;
  setting: InputSetting;
  subNodeId?: number;
}

export interface IInputInfo {
  input: NodeInput;
  slot: number;
  link_pos: [number, number];
  locked: boolean;
}

export interface IOutputInfo {
  output: NodeOutput;
  slot: number;
  link_pos: [number, number];
  locked: boolean;
}


export const convertStringToType = (type: string): Type => {
  switch (type) {
    case Type.STRING:
      return Type.STRING;
    case Type.NUMBER:
      return Type.NUMBER;
    case Type.BOOLEAN:
      return Type.BOOLEAN;
    case Type.JSON:
      return Type.JSON;
  }
  return Type.ANY;
};

export const convertType = (type: Type): SettingType => {
  switch (type) {
    case Type.STRING:
      return SettingType.STRING;
    case Type.NUMBER:
      return SettingType.NUMBER;
    case Type.BOOLEAN:
      return SettingType.BOOLEAN;
    case Type.DROPDOWN:
      return SettingType.DROPDOWN;
  }
  return SettingType.STRING;
};
