import { Node, SettingType } from '../../node';
import { Type } from '../../node-io';
import { Container, Side } from '../../container';
import tagRefs from '../../utils/points/point-ref-utils';
import { isNull } from '../../../utils/helper';

class PointTagNode extends Node {
  deviceId = 0;
  pointId = 1;
  clientId = 2;
  siteId = 3;
  clientName = 4;
  siteName = 5;
  siteAddress = 6;
  equipRef = 7;
  
  constructor() {
    super();
    this.title = 'Point Tag Node';
    this.description = 'A node generating HVAC tags';
    this.addInputWithSettings('deviceId', Type.STRING, 'device id', 'device id', false);
    this.addInputWithSettings('pointId', Type.STRING, 'point id', 'point id/name', false);
    this.addInputWithSettings('clientId', Type.STRING, 'client id', 'client id', false);
    this.addInputWithSettings('clientName', Type.STRING, 'client name', 'Client Name', false);
    this.addInputWithSettings('siteId', Type.STRING, 'site id', 'site id', false);
    this.addInputWithSettings('siteName', Type.STRING, 'site name', 'Site/Building Name', false);
    this.addInputWithSettings('siteAddress', Type.STRING, '123 my street', 'Site/Building Address', false);
    this.addInputWithSettings('equipRef', Type.STRING, 'FCU123', 'Equipment Reference', false);
    this.addOutput('output', Type.JSON);
    this.settings['equipType'] = {
      description: 'Equipment Type',
      type: SettingType.DROPDOWN,
      config: {
        items: tagRefs.tagCategory,
      },
    };
    this.settings['tags'] = {
      description: 'Point Type',
      value: null,
      type: SettingType.DROPDOWN,
    };
    this.settings['tags_values'] = {
      description: 'Auto generated tags values',
      value: null,
      type: SettingType.READONLY,
    };
  }


  onAdded() {
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    const deviceId = this.getInputData(this.deviceId);
    const pointId = this.getInputData(this.pointId);
    const clientId = this.getInputData(this.clientId);
    const clientName = this.getInputData(this.clientName);
    const siteId = this.getInputData(this.siteId);
    const siteName = this.getInputData(this.siteName);
    const siteAddress = this.getInputData(this.siteAddress);
    const equipRef = this.getInputData(this.equipRef);
    const equipType = this.settings['equipType'].value;
    let tagSetting = tagRefs.tagType(equipType)
    if (isNull(tagSetting)) return;
    this.settings['tags'].config = {
      items: Object.keys(tagSetting),
    };
    let pointType = this.settings['tags'].value
    let tags: any;
    for (var key in tagSetting) {
      if (key == pointType) {
        tags = tagSetting[key]
      }
    };
    let pointArr = {pointType:pointType, equipRef:equipRef, equipType: equipType, deviceId: deviceId, pointId: pointId, clientId: clientId, clientName: clientName, siteId: siteId, siteName: siteName, siteAddress: siteAddress }

    if (tags) {
      this.settings['tags_values'].value = tags.tags
      this.broadcastSettingsToClients();
      tags = tags.tags
      let pointTags = {}
      tags.forEach(x => { pointTags[x] = x })
      const out = { ...pointArr, ...pointTags }
      this.setOutputData(0, out);
    }
  }
  onAfterSettingsChange() {
    this.onInputUpdated();
  }


}
Container.registerNodeType('history/point-tags', PointTagNode);
