import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathSinNode extends Node {
  constructor() {
    super();

    this.title = 'Sin';
    this.description = "'sin(x)' is the result of sin('x').  The Sine function of input 'x'.";
    this.addInput('x', Type.NUMBER);
    this.addOutput('sin(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.sin(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/sin', MathSinNode);
