import { Node, NodeState } from '../../node';
import { Container } from '../../container';

class NotesNode extends Node {
  constructor() {
    super();
    this.title = 'Comments';
    this.description = 'This node is simply for adding some code comments';
  }

  onAdded() {
    this.nodeColour();
  }

  nodeColour() {
    this.setNodeState(NodeState.INFO);
  }
}
Container.registerNodeType('debug/comments', NotesNode);
