import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class MathDecimalPlaceNode extends Node {

  constructor() {
    super();
    this.title = 'Round (Decimal Place)';
    this.description =
      "'output' is the 'input' value rounded to the configured number of decimal places.  The number of decimal places can be configured from settings (limited to 5 decimal places).";
    this.addInput('input', Type.NUMBER);
    this.addOutput('output', Type.NUMBER);
    this.settings['decimals'] = {
      description: 'Decimal Places (Limit 5)',
      value: 1,
      type: SettingType.NUMBER,
    };
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let decimals = this.settings['decimals'].value;
    if (decimals > 5) decimals = 5;
    this.emitTransformedInput(x => x.toFixed(decimals));
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('num-transform/round', MathDecimalPlaceNode);
