import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class TextASCIICodeNode extends Node {
  constructor() {
    super();

    this.title = 'Char to ASCII Code';
    this.description =
      "'output' is the ASCII code of the first character of a given String 'input' value.";

    this.addInput('input', Type.STRING);
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (val != null) this.setOutputData(0, val.charCodeAt(0));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('string/char-to-ascii', TextASCIICodeNode);
