import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';

class MinNode extends Statistics {
  constructor() {
    super();
    this.title = 'Min';
    this.description =
      'Outputs the Minimum value of all the (non null) Numeric inputs. The number of inputs and their values can be modified from settings.';
    this.addOutput('min', Type.NUMBER);
  }

  onInputUpdated() {
    const data = this.getDefinedInputsOrSettingsValues();
    const min = Utils.stat.min(data);
    this.setOutputData(0, min);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('statistics/min', MinNode);
