import { Node, SettingType} from '../../node';
import { Container, Side } from '../../container';
import { Type } from '../../node-io';

//Bool Constant
class BoolConstantNode extends Node {

  constructor() {
    super();
    this.title = 'Boolean Constant';
    this.description = 'Outputs a Boolean value set from settings.  Can also output null.';

    this.settings['value'] = {
      description: 'Payload',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'false' },
          { value: true, text: 'true' },
          { value: null, text: 'null' },
        ],
      },
      value: null,
    };

    this.addOutput('output', Type.BOOLEAN);
  }

  onAdded() {
 
    if (this.side != Side.server) return;
    let val = this.settings['value'].value;
    this.setOutputData(0, val);
  }

  onExecute(){
    // this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    let val = this.settings['value'].value;
    this.setOutputData(0, val);
  }
}
Container.registerNodeType('point/bool-constant', BoolConstantNode);
