import { Node, SettingType} from '../../../node';
import { Container, Side } from '../../../container';
import * as nodemailer from 'nodemailer';
import { Type } from '../../../node-io';
import CryptoUtils from '../../../utils/crypto-utils';
import Utils from '../../../utils';

class EmailNode extends Node {
  lastTrigger: boolean;
  // Supported services: https://github.com/nodemailer/nodemailer/blob/master/lib/well-known/services.json
  services = ['Gmail', 'Hotmail', 'Outlook365', 'Yahoo'].map(service => {
    return { value: service, text: service };
  });
  constructor() {
    super();
    this.title = 'Email';
    this.description =
      "Once configured (in settings) with the sender email server details ('email' and 'password') for Gmail, Hotmail, Outlook365, or Yahoo accounts: This node will attempt to send a new email when 'trigger' transitions from 'false' to 'true'.   The String 'recipient' value will be the to: email address; The String 'subject' value will be the email subject; and the String 'message' value will be the email body.  The 'response' output will be 'true' if the email is sent successfully.  The 'error' output will be an error message String if there is an error sending the email. ";
    this.settings['service'] = {
      description: 'Service',
      value: this.services[0].value,
      type: SettingType.DROPDOWN,
      config: {
        items: this.services,
      },
    };
    this.settings['user'] = { description: 'Email', value: '', type: SettingType.STRING };
    this.settings['pass'] = { description: 'Password', value: '', type: SettingType.PASSWORD };
    this.addInput('trigger', Type.BOOLEAN);
    this.addInputWithSettings('recipient', Type.STRING, '', 'To', false);
    this.addInputWithSettings('subject', Type.STRING, '', 'Subject', false);
    this.addInputWithSettings('message', Type.STRING, '', 'Message', false);
    this.addOutput('response', Type.STRING);
    this.addOutput('error', Type.STRING);
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server

    let trigger = this.getInputData(0);
    if (!Utils.hasInput(trigger)) trigger = false;
    if (trigger == this.lastTrigger) return;
    else this.lastTrigger = trigger;
    if (trigger == false) return;

    const service = this.settings['service'].value;
    const user = this.settings['user'].value;
    const from = user;
    const pass = this.settings['pass'].value;
    const to = this.getInputData(1);
    const subject = this.getInputData(2);
    const text = this.getInputData(3);

    const transporter = nodemailer.createTransport({
      service,
      auth: { user, pass: CryptoUtils.decrypt(pass) },
    });

    transporter.sendMail({ from, to, subject, text }, error => {
      if (error) {
        this.emitError(error.message);
      } else {
        this.emitResult(true);
      }
    });
  }

  emitResult(result) {
    this.setOutputData(0, result);
    this.setOutputData(1, null);
  }

  emitError(body) {
    this.setOutputData(0, null);
    this.setOutputData(1, body);
  }
}

Container.registerNodeType('protocols/misc/email', EmailNode);
