import * as NeDBDataStore from 'nedb';

import HistorianDatabase from '../database/HistorianDatabase';
import { History } from '../models/historian';
import config from '../../config';

const log = require('logplease').create('database', { color: 4 });

export default class HistorianDataSource implements HistorianDatabase {
  private history: NeDBDataStore;

  async asyncLoadDatabase(cleanUpInterval: any): Promise<any> {
    const path = config.dataDir;
    this.history = new NeDBDataStore(`${path}/history.db`);
    this.history.persistence.setAutocompactionInterval(cleanUpInterval);
    return this.loadHistory();
  }

  private loadHistory(): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.history.loadDatabase(err => {
        if (err) {
          log.error(err);
          reject(err);
        }
        return resolve();
      });
    });
  }

  addHistory(history: History, callback?: (err?: Error, doc?: History) => void) {
    this.history.insert(history, function(err, doc) {
      if (err) log.error(err);
      if (callback) callback(err, doc);
    });
  }

  updateHistory(
    query: object,
    history: History,
    callback?: (err?: Error) => void,
    updatedCallback?: (updated?: number) => void,
  ) {
    this.history.update(query, history, {}, function(err, updated) {
      if (err) log.error(err);
      else {
        updatedCallback(updated);
      }
      if (updated == 0) log.error(`Can't update history [${name}]. Document not found.`);
      if (callback) callback(err);
    });
  }

  //remove
  removeHistory(
    query: object,
    callback?: (err?: Error) => void,
    removedCallback?: (removed?: number) => void,
  ) {
    this.history.remove(query, {}, function(err, removed) {
      if (err) {
        log.error(err);
      } else {
        removedCallback(removed);
      }
      if (removed == 0) log.debug("Cat't remove. History Document not found.");
      if (callback) callback(err);
    });
  }
}

