import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathPowNode extends Node {

  constructor() {
    super();

    this.title = 'Power';
    this.description =
      "('output' = 'in 1' ^ 'in 2') 'output' is the result of 'in 1' to the power of 'in 2'. Input values can be set from settings.";

    this.addInputWithSettings('in 1', Type.NUMBER, 0, 'in 1');
    this.addInputWithSettings('in 2', Type.NUMBER, 1, 'in 2');
    this.addOutput('output', Type.NUMBER);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let x = this.getInputData(0);
    let y = this.getInputData(1);
    if (x != null && y != null) this.setOutputData(0, Math.pow(x, y));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math/power', MathPowNode);
