import { Type } from '../../node-io';
import { UiNode } from './ui-node';
import { Container } from '../../container';

export class UiProgressNode extends UiNode {
  constructor(container?: Container, id?: number, properties?: any) {
    super('Progress', 'UiProgressNode', properties);
    this.description =
      "Dashboard node which displays 'input' value from 0-100 on a progress bar on the dashboard.";
    this.title = 'Progress';
    this.addInput('input', Type.NUMBER);
    this.UPDATE_INPUTS_INTERVAL = 100;
    this.setState(0);
  }

  onInputUpdated() {
    let val = this.getInputData(0);

    if (val > 100) val = 100;
    if (val < 0) val = 0;

    this.setState(val);
    this.isRecentlyActive = true;
  }
}

Container.registerNodeType('dashboard/progress', UiProgressNode);
