import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathLogarithmNode extends Node {
  constructor() {
    super();

    this.title = 'Logarithm (Base E)';
    this.description =
      "'log(x)' is the result of log('x') with base E (Euler's Number 2.71828).  The Natural Logarithmic function of input 'x'.";

    this.addInput('x', Type.NUMBER);
    this.addOutput('log(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.log(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/log', MathLogarithmNode);
