import { Node, SettingType } from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class FiltersPreventEqualNode extends Node {
  constructor() {
    super();

    this.title = 'Prevent Equal';
    this.description =
      "This node filters 'input' values.  All 'input' values are passed to 'output' EXCEPT 'input' values which are equal to 'match'.";

    this.addInput('input', Type.ANY);
    this.addInputWithSettings('match', Type.ANY, null, 'Match Value');
    this.addOutput('output', Type.ANY);
    this.properties['value'];
  }

  onAdded(){
    this.setOutputData(0, this.properties['value']);
    this.onInputUpdated();
  }

  onAfterSettingsChange(){
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    val = (val || '').toString(); // making string null i.e `''` and other `null` same

    let match = (this.getInputData(1) || '').toString();
    if (match == 'false') match = false;
    else if (match == 'true') match = true;

    if (val != match) {
      this.setOutputData(0, val);
      this.properties['value'] = val;
      this.persistProperties(false, true);
    }
  }
}
Container.registerNodeType('filter/prevent-equal', FiltersPreventEqualNode);
