import { SettingType } from '../../node';
import { ContainerNode } from '../../container-node';
import { Container, Side } from '../../container';
import { Type } from '../../node-io';
import OsUtils from '../../utils/os-utils';
import FileUtils from '../../utils/file-utils';
import UuidUtils from '../../utils/uuid-utils';
import config from '../../../config';

class PlatNode extends ContainerNode {
  // haystack site
  haystackSite = {
    id: { description: 'enter site id', value: 'My ID', type: SettingType.STRING },
  };

  haystackAbout = {
    serverName: OsUtils.hostName(),
    productVersion: OsUtils.getCurrentWiresVersion(),
  };

  // global settings
  globalSettings = {
    units: ['metric, imperial'],
    vendorName: 'Nube IO',
  };

  constructor(container) {
    super(container);
    this.title = 'Platform';
    this.description =
      'This node provides global settings for the Wires instance. It should be added to the main(root) Editor Pane of each Wires instance. ‘Client ID’ and ‘Device ID’ are used by each node with a history logging configuration to store data.';

    this.addInputWithSettings('clientId', Type.STRING, 'client id', 'client id', false);
    this.addInputWithSettings('siteId', Type.STRING, 'site id', 'site id', false);
    this.addInputWithSettings('clientName', Type.STRING, 'client name', 'Client Name', false);
    this.addInputWithSettings('siteName', Type.STRING, 'site name', 'Site/Building Name', false);
    this.addInputWithSettings('siteAddress', Type.STRING, '123 my street', 'Site/Building Address', false);
    this.addOutput('device-id', Type.STRING);
    this.addOutput('client-id', Type.STRING);
    this.addOutput('site-id', Type.STRING);
    this.addOutput('wires-version', Type.STRING);
    this.addOutput('output-json', Type.JSON);
    this.settings['deviceIdType'] = {
      description: 'Device ID Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: false, text: 'Fixed ID' },
          { value: true, text: 'Custom ID' },
        ],
      },
      value: false,
    };

    this.settings['deviceIDFixed'] = {
      description: 'Fixed Device ID',
      value: 'unknownDeviceID',
      type: SettingType.READONLY,
    };

    this.settings['deviceIDcustom'] = {
      description: 'Custom Device ID',
      value: '',
      type: SettingType.STRING,
    };

    this.settings['serverName'] = {
      description: 'serverName',
      value: '',
      type: SettingType.READONLY,
    };
    this.settings['productVersion'] = {
      description: 'productVersion',
      value: '',
      type: SettingType.READONLY,
    };

    this.setSettingsConfig({
      groups: [],
      conditions: {
        deviceIDFixed: setting => {
          return !setting['deviceIdType'].value;
        },
        deviceIDcustom: setting => {
          return !!setting['deviceIdType'].value;
        },
      },
    });

    this.properties['deviceID'] = {};
  }

  async onAdded() {
    super.onAdded();
    await this.haystackAbout.serverName.then(e => {
      this.settings['serverName'].value = e;
      this.broadcastSettingsToClients();
    });
    await this.haystackAbout.productVersion.then(e => {
      this.settings['productVersion'].value = e;
      this.broadcastSettingsToClients();
      this.setOutputData(3, e); // wires version
    });

    await this.onAfterSettingsChange(null);
  }

  async onInputUpdated() {
    await this.onAfterSettingsChange(null);
  }

  async onAfterSettingsChange(oldSettings) {
    super.onAfterSettingsChange(oldSettings);
    if (this.side !== Side.server) return; // only execute on server
    await this.doUUIDSteps();
    this.properties['deviceID'] = this.settings['deviceIdType'].value
      ? this.settings['deviceIDcustom'].value
      : this.settings['deviceIDFixed'].value;
    this.persistSettings();
    this.setOutputData(0, this.properties['deviceID']);
    this.setOutputData(1, this.getInputData(0)); // client-id
    this.setOutputData(2, this.getInputData(1)); // site-id
    const out = {
      deviceID: this.properties['deviceID'],
      clientID: this.getInputData(0), // client-id
      siteID: this.getInputData(1), // site-id
      clientName: this.getInputData(2), // client-name
      siteName: this.getInputData(3), // site-name
      siteAddress: this.getInputData(4), // site-address
    };
    this.setOutputData(4, out); // json
  }

  persistSettings() {
    if (!this.container.db) return;
    this.container.db.updateNode(this.id, this.container.id, {
      $set: {
        settings: this.settings,
        properties: this.properties,
      },
    });
  }

  async doUUIDSteps() {
    const fileName = 'deviceUUID.txt';
    const dirPath = `${config.dataDir}/ids`;
    const filePath = `${dirPath}/${fileName}`;
    const isOldFileExist = await FileUtils.checkForOldDirectory(filePath);
    if (!isOldFileExist) {
      const isUUIDFile = await UuidUtils.isUUIDFile(filePath);
      if (!isUUIDFile) {
        try {
          await UuidUtils.makeUUIDFile(dirPath, fileName);
        } catch (e) {
          this.debugErr(e);
        }
      }
    }
    await this.getDeviceUUID(filePath);
  }

  async getDeviceUUID(filePath) {
    try {
      const data = await FileUtils.readFile(filePath);
      this.settings['deviceIDFixed'].value = data.toString().trim();
      this.broadcastSettingsToClients();
    } catch (e) {
      this.debugWarn(e);
    }
  }
}
Container.registerNodeType('system/platform', PlatNode);
