import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class SystemBeepNode extends Node {
  constructor() {
    super();
    this.title = 'Beep';
    this.description =
      'This node plays a default system sound on the server (not in the browser) when ‘trigger’ transitions from ‘false’ to ‘true’.';

    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('error', Type.STRING);
  }

  onInputUpdated() {
    try {
      if (this.getInputData(0) == true) process.stderr.write('\x07');
      this.setOutputData(0, false);
    } catch (e) {
      this.setOutputData(0, true);
    }
  }
}
Container.registerNodeType('system/beep', SystemBeepNode);
