import { Node, SettingType } from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import { isJSON, isEmpty } from '../../../utils/helper';
class JsonConstantNode extends Node {

  constructor() {
    super();
    this.title = 'Json Constant';
    this.description = `Outputs a JSON value set from settings. Can also output null by leaving the settings field blank. Example {"name":"John Hill", "age":31} or [1,2,3,4]`;
    this.addInputWithSettings('value', Type.JSON, '{ "measurement": 22 }', 'JSON value');
    this.settings['passNull'] = {
      description: 'Send null if not a match',
      value: false,
      type: SettingType.BOOLEAN,
    };
    this.addOutput('output', Type.JSON);
    this.addOutput('valid-json', Type.BOOLEAN);
    this.addOutput('msg', Type.STRING);
  }

  onAdded() {
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    const passNull = this.settings['passNull'].value;
    try {
      if (isJSON(val) && !isEmpty(val)) {
        this.setOutputData(0, val);
        this.setOutputData(1, true);
        this.setOutputData(2, 'valid JSON');
      } else if(passNull) {
        this.setOutputData(0, null);
        this.setOutputData(1, false);
        this.setOutputData(2, 'invalid JSON');
      } else {
        this.setOutputData(1, false);
        this.setOutputData(2, 'invalid JSON');
      }
    } catch (err) {
      this.setOutputData(1, false);
      this.setOutputData(2, err);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated()
  }
}
Container.registerNodeType('point/json-constant', JsonConstantNode);
