import * as fs from 'fs';
import config from '../../config';
const log = require('logplease').create('auth-database', { color: 4 });
import FileUtils from '../../nodes/utils/file-utils';

const file = `${config.dataDir}/platform.db`;
export default class PlatformDataSource {

    static getPlatform(): Promise<any> {
        return new Promise((resolve, reject) => {
            FileUtils.createFile(file).then(() => {
                fs.readFile(file, { encoding: 'utf-8' }, (err, data) => {
                    if (err) {
                        reject(err);
                        log.error(err);
                    } else {
                        const obj = data ? JSON.parse(data) : {};
                        resolve(obj);
                    }
                });
            });
        });
    }

    static updatePlatform(data: any): Promise<any> {
        return new Promise((resolve, reject) => {
            FileUtils.createFile(file).then(() => {
                var json = JSON.stringify(data);
                fs.writeFile(file, json, 'utf-8', err => {
                    if (err) {
                        reject(err);
                        log.error(err);
                    } else {
                        resolve(json);
                    }
                });
            });
        });
    }

}

