import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';

class LessThanNode extends Node {

  constructor() {
    super();

    this.title = 'Less than';
    this.description =
      "'out <' is 'true' when 'in 1' is less than 'in 2', otherwise 'out <' is 'false'.  'out <=' is 'true' when 'in 1' is less than or equal to 'in 2', otherwise 'out <=' is 'false'.  Both outputs will be 'false' if either input is undefined.";
    this.addInput('in 1', Type.NUMBER);
    this.addInputWithSettings('in 2', Type.NUMBER, 0, 'In 2 Constant', false);
    this.addOutput('out <', Type.BOOLEAN);
    this.addOutput('out <=', Type.BOOLEAN);
  }

  onAdded() {
 
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    let a = this.getInputData(0);
    let b = this.getInputData(1);

    if (a != null && b != null) {
      this.setOutputData(0, a < b);
      this.setOutputData(1, a <= b);
      return;
    } else {
      this.setOutputData(0, false);
      this.setOutputData(1, false);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('compare/less-than', LessThanNode);
