import { UiNode } from './ui-node';
import { Container } from '../../container';
import Utils from '../../utils';

export class UiLabelNode extends UiNode {
  constructor(container?: Container, id?: number, properties?: any) {
    super('Label', 'UiLabelNode', properties);
    this.description = 'Dashboard node which displays inputed values on the dashboard.';
    this.title = 'Label';
    this.addInput('input');
    this.UPDATE_INPUTS_INTERVAL = 100;
  }

  onInputUpdated() {
    const state = Utils.formatValue(this.getInputData(0));
    this.setState(state);
    this.isRecentlyActive = true;
  }
}

Container.registerNodeType('dashboard/label', UiLabelNode);
