import { Node, SettingType } from '../../node';
import { Container, Side } from '../../container';
import { Type } from '../../node-io';
import { isJSON, isNull } from '../../../utils/helper';
let moment = require('moment-timezone');
const Influx = require('influx');
import config from '../../../config';

class InfluxDBNode extends Node {
  selectedTags;
  client
  constructor() {
    super();
    this.title = 'Influx-DB';
    this.description = 'A node for sending data to influxDB';
    this.addInput('[name]', Type.STRING);
    this.addInput('json-input', Type.JSON);
    this.addOutput('error', Type.BOOLEAN);
    this.addOutput('lastExport', Type.STRING);
    this.settings['enable'] = { description: 'Enable', value: false, type: SettingType.BOOLEAN };
    this.settings['useEnv'] = { description: 'Dont use env settings', value: false, type: SettingType.BOOLEAN };
    this.settings['host'] = { description: 'Host', value: '0.0.0.0', type: SettingType.STRING };
    this.settings['port'] = { description: 'Port', value: '8086', type: SettingType.STRING };
    this.settings['authentication'] = {
      description: 'Use Authentication',
      value: false,
      type: SettingType.BOOLEAN,
    };
    this.settings['user'] = { description: 'Username', value: '', type: SettingType.STRING };
    this.settings['password'] = { description: 'Password', value: '', type: SettingType.STRING };
    this.settings['databaseName'] = {
      description: 'Database Name',
      value: '',
      type: SettingType.STRING,
    };
    this.setSettingsConfig({
      groups: [
        { host: { weight: 3 }, port: { weight: 1 } },
        { user: {}, password: {} },
        { period: { weight: 2 }, periodUnits: {} },
      ],
      conditions: {
        host: setting => {
          return !!setting['useEnv'].value;
        },
        port: setting => {
          return !!setting['useEnv'].value;
        },
        authentication: setting => {
          return !!setting['useEnv'].value;
        },
        databaseName: setting => {
          return !!setting['useEnv'].value;
        },
        user: setting => {
          return !!setting['authentication'].value;
        },
        password: setting => {
          return !!setting['authentication'].value;
        },
      },
    });
  }
  onAdded() {
    this.createClient();
  }

  async createClient() {
    let { protocol, host, port, databaseName, username, password } = config.influxDb;
    if (this.settings['useEnv'].value) {  //dont use env
      host = this.settings['host'].value;
      port = this.settings['port'].value;
      this.client = new Influx.InfluxDB({
        host: this.settings['host'].value || 'localhost',
        port: this.settings['port'].value || 8086,
        protocol: 'http', //this.influxdbConfig.usetls ? "https" : "http",
        database: this.settings['databaseName'].value || 'undefined',
        username: this.settings['authentication'].value ? this.settings['user'].value : '',
        password: this.settings['authentication'].value ? this.settings['password'].value : '',
      });
    } else { //use use env
      this.client = new Influx.InfluxDB({
        host,
        port,
        protocol: protocol === 'https' ? 'https' : 'http',
        database: databaseName,
        username,
        password,
      });
    }
  };

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    if (!this.client) return;
    if (!this.settings['enable'].value) return;

    //update node name
    if (this.inputs[0].updated) {
      let nodeName = this.getInputData(0)
      if (!isNull(nodeName)) {
        this.name = nodeName;
        this.broadcastNameToClients()
      };
    }

    const input = this.getInputData(1);
    if (isNull(input)) return;
    const measurementHas = input.hasOwnProperty('measurement')
    const measurementTags = input.hasOwnProperty('tags')
    const measurementFields = input.hasOwnProperty('fields')
    if (!input) return;
    if (input === !isJSON) {
      this.debugInfo(`INFLUX-DB NODE: input isn't JSON`);
      return
    };
    let tagObj: any;
    try {
      if (isNull(input)) return;
      tagObj = JSON.parse(input.tags);
    } catch (err) {
      this.debugInfo(`INFLUX-DB NODE: try/catch  json parse of tags, ${err}`);
    };
    if (isNull(input)) return;
    if (measurementHas && measurementTags && measurementFields) {
      this.setOutputData(0, false)
      this.setOutputData(1, moment().format())
      this.client.writePoints([
        {
          measurement: input.measurement,
          tags: tagObj,
          fields: input.fields
        }]).catch(err => {
          this.debugInfo(`INFLUX-DB NODE: error on influx write, ${err}`);
          this.setOutputData(0, true)
        });
    }
  };

  onAfterSettingsChange() {
    if (this.side !== Side.server) return; // only execute on server
    this.createClient();
    this.onInputUpdated();
  };

}
Container.registerNodeType('history/influx-db', InfluxDBNode);
