import { Container } from './container';
import Utils from './utils';

export default class NodeUtils {
  static getNodesIOValues(cid: number): any {
    let container = Container.containers[cid];
    if (!container || !container._nodes) return;

    let inputs_values = [];
    let outputs_values = [];
    for (let id in container._nodes) {
      let node = container._nodes[id];
      if (node.inputs) {
        for (let i in node.inputs) {
          let data = node.getInputData(Number(i));
          data = Utils.formatAndTrimValue(data);
          inputs_values.push({
            nodeId: node.id,
            inputId: i,
            type: node.type,
            data,
          });
        }
      }

      if (node.outputs) {
        for (let o in node.outputs) {
          let data = node.outputs[o].data;
          data = Utils.formatAndTrimValue(data);
          outputs_values.push({
            nodeId: node.id,
            outputId: o,
            type: node.type,
            data,
          });
        }
      }
    }
    return {
      cid: cid,
      inputs: inputs_values,
      outputs: outputs_values,
    };
  }
}
