import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';

class RangeNode extends Statistics {
  constructor() {
    super();
    this.title = 'range';
    this.description =
      'Outputs the Range value (max-min) of all the (non null) Numeric inputs. The number of inputs and their values can be modified from settings.';
    this.addOutput('range', Type.NUMBER);
  }

  onInputUpdated() {
    const data = this.getDefinedInputsOrSettingsValues();
    const range = Utils.stat.range(data);
    this.setOutputData(0, range);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('statistics/range', RangeNode);
