import { Container } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import Statistics from './statistics';

class MaxNode extends Statistics {
  constructor() {
    super();
    this.title = 'Max';
    this.description =
      'Outputs the Maximum value of all the (non null) Numeric inputs. The number of inputs and their values can be modified from settings.';
    this.addOutput('max', Type.NUMBER);
  }

  onInputUpdated() {
    const data = this.getDefinedInputsOrSettingsValues();
    const max = Utils.stat.max(data);
    this.setOutputData(0, max);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('statistics/max', MaxNode);
