import { Node, SettingType} from '../../node';
import { Container, Side } from '../../container';
import { Type } from '../../node-io';
import Utils from '../../utils';
import axios from 'axios';
import CryptoUtils from '../../utils/crypto-utils';
import TimeUtils from '../../utils/time-utils';

class NubeLoginNode extends Node {
  constructor() {
    super();
    this.title = 'Login';
    this.description =
      "This node is used to authenticate with Nube devices.  Once successfully authenticated, the other Nube-API nodes will have their settings updated with values from the connected Nube devices. ‘Host’ setting is the IP address of the Nube controller to connect to.  ‘Port’ setting is the port at ‘Host’ (standard port is 4000). ‘Username’ setting is the username for the ‘Host’ (standard is ‘admin’).  ‘Password’ setting is the password for the ‘Host’; this password is supplied with each Nube controller.  ‘Interval’ is the authentication frequency (minimum 10 mins). 'interval’ units can be configured from settings.  Maximum ‘interval’ setting is 587 hours.";

    this.settings['host'] = { description: 'Host', value: '', type: SettingType.STRING };
    this.settings['port'] = { description: 'Port', value: '', type: SettingType.STRING };
    this.settings['username'] = { description: 'Username', value: '', type: SettingType.STRING };
    this.settings['password'] = { description: 'Password', value: '', type: SettingType.PASSWORD };

    this.addInput('trigger', Type.BOOLEAN);
    this.addInputWithSettings('interval', Type.NUMBER, 10, 'Interval(minimum 10 minutes)', false);
    this.addOutput('jwt', Type.STRING);
    this.addOutput('error', Type.STRING);
    this.settings['time'] = {
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 'milliseconds', text: 'Milliseconds' },
          { value: 'seconds', text: 'Seconds' },
          { value: 'minutes', text: 'Minutes' },
          { value: 'hours', text: 'Hours' },
        ],
      },
      value: 'minutes',
    };
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  onAdded() {
    this.setExecuteInterval();
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
  }

  async onInputUpdated() {
    this.setExecuteInterval();
    let trigger = this.getInputData(0);
    if (trigger && this.inputs[0].updated) {
      await this.getJwtToken();
    }
  }

  async onExecute() {
    if (this.side !== Side.server) return;
    await this.getJwtToken();
  }

  async onAfterSettingsChange() {
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    this.setExecuteInterval();
    await this.getJwtToken();
  }

  setExecuteInterval() {
    let interval = this.getInputData(1);
    interval = TimeUtils.timeConvert(interval, this.settings['time'].value);
    interval = Math.max(interval, 600000);
    this.EXECUTE_INTERVAL = interval;
  }

  onRemoved() {
    if (this.side !== Side.server) return; // only execute on server
    return new Promise((resolve, reject) => {
      this.container &&
        this.container.db.removeUser({ nodeId: this.id }, err => {
          if (err) {
            reject(err);
          } else {
            resolve();
          }
        });
    });
  }

  private async getJwtToken(): Promise<any> {
    if (this.side !== Side.server) return;
    const host = this.settings['host'].value;
    const port = this.settings['port'].value;
    const url = `${Utils.buildUrl(host, port)}/user/login`;

    const username = this.settings['username'].value;
    const password = this.settings['password'].value;
    const body = { username, password: CryptoUtils.decrypt(password) };
    try {
      const res = await axios.post(url, body);
      const query = { nodeId: this.id };
      const user = {
        jwt: res.data.token,
        host,
        port,
        nodeId: this.id,
      };
      await this.container.db.addOrUpdateUser(query, user);
      this.setOutputData(0, res.data.token);
      this.setOutputData(1, null);
    } catch (e) {
      this.setOutputData(0, null);
      this.setOutputData(1, e.message);
      await this.onRemoved();
    }
    return;
  }
}

Container.registerNodeType('nube/login', NubeLoginNode);
