const ip = require('ip'); //for checking if IPs are valid
const network = require('network'); //for getting IPs and gateway address
import { exec } from 'child_process';
const cp = require('child_process');
export default class IpAddressUtils {
  /**
   * Get current network info
   * @returns {object}
   */
  static getActiveNetworks() {
    return new Promise<Array<any>>(resolve => {
      network.get_interfaces_list(function(err, obj) {
        if (err) resolve();
        if (!obj) resolve();
        resolve(obj);
      });
    });
  }

  /**
   * Runs a system command
   * @param iface {string} Name of the physical system interface
   * @param upDown {string} system command options
   * @returns {Promise}
   *
   */

  static networkUpDown(iface: string, upDown: string) {
    return new Promise((resolve, reject) => {
      let command = `sudo ip link set ${iface} ${upDown}`;
      // let command = 'sudo ip link set' +' '+ iface +' '+upDown
      exec(command, (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(stdout);
        }
      });
    });
  }

  /** check if new ip is valid
   * @description Method for checking and build an ip address
   * @param {string} newIP pass in an ip address
   * @param {string} netNetmask pass in an ip net mask
   * @param {string} newGateway pass in an  ip gateway
   * @param newNameserver
   * @returns {object} Will return a validIp: true, ipSubnet: '192.168.15.1/24 or if invalid it will return false
   */

  static checkIpValid(newIP: string, netNetmask: string, newGateway: string, newNameserver: string) {
    const checkIP = ip.isV4Format(newIP);
    const checkNetMask = ip.isV4Format(netNetmask);
    const checkGateway = ip.isV4Format(newGateway);
    const nameserver = ip.isV4Format(newNameserver);

    if (checkIP && checkNetMask && checkGateway && nameserver) {
      return true;
    } else return false;
  }

  /**
   * Gets BBB interface details
   * @returns {Promise}
   *
   */
  static bbbGetInterfaceDetails() {
    return new Promise((resolve, reject) => {
      cp.execSync('connmanctl services', (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(
            stdout
              .toString()
              .replace(/\s/g, '')
              .substring(8),
          );
        }
      });
    });
  }

  /**
   * Set BBB network as DHCP
   * @returns {Promise}
   *
   */
  static bbbSetIPDHCP(iface) {
    return new Promise((resolve, reject) => {
      const setIpDHCP = `sudo connmanctl config ${iface} --ipv4 dhcp`;
      cp.execSync(setIpDHCP, (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(true);
        }
      });
    });
  }

  /**
   * Set BBB network as fixed IP
   * @returns {Promise}
   *
   */
  static bbbSetIPFixed(iface, ipAddress: string, subnetMask: string, gateway: string) {
    return new Promise((resolve, reject) => {
      const setIP = `sudo connmanctl config ${iface} --ipv4 manual ${ipAddress} ${subnetMask} ${gateway} --nameservers 8.8.8.8 8.8.4.4`;
      cp.execSync(setIP, (err, stdout, stderr) => {
        if (err) {
          reject({ err, stderr });
        } else {
          resolve(true);
        }
      });
    });
  }
}
