import { Node} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
//import Icons from '../../node-icons';

//let uiMainIcon = Icons.uiMainIcon;
//let uiMainColour = Icons.uiMainColour;

class MathCosNode extends Node {
  constructor() {
    super();

    this.title = 'Cos';
    this.description = "'cos(x)' is the result of cos('x').  The Cosine function of input 'x'.";
    this.addInput('x', Type.NUMBER);
    this.addOutput('cos(x)', Type.NUMBER);
  }

  onAdded() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    let val = this.getInputData(0);
    if (val != null) this.setOutputData(0, Math.cos(val));
    else this.setOutputData(0, null);
  }
}
Container.registerNodeType('math-advanced/cos', MathCosNode);
