import { Node, SettingType } from '../../node';
import { User } from '../../../database/models/user';
import Utils from '../../utils';

export class BaseEdge extends Node {
  protected constructor() {
    super();
    this.settings['url'] = {
      description: 'Select Url',
      value: '',
      type: SettingType.DROPDOWN,
      config: {
        items: [],
      },
    };
  }

  protected async fetchConnection(suffixApi: string): Promise<User> {
    let user = null;

    const users = await this.container.db.getAsyncUsers();
    const urls = users.map(user => {
      return {
        value: JSON.stringify(user),
        text: `${Utils.buildUrl(user.host, user.port)}${suffixApi} (${user.nodeId})`,
      };
    });
    this.settings['url'].config.items = urls;
    const url = this.settings['url'].value;
    if (url && urls.map(x => x.value).includes(url)) {
      user = this.settings['url'].value;
    } else {
      user = urls.length && urls[0].value;
      this.settings['url'].value = user;
    }
    this.broadcastSettingsToClients();

    return (user && JSON.parse(user)) || null;
  }
}
