import { Node, SettingType } from './node';
import { IOT_TYPES } from './constants';
import { Container } from './container';

// This container node needs its own file to prevent a cyclic dependency with `Container` that the TS compiler cannot resolve

//Container: special node that contains a container of other nodes
export class ContainerNode extends Node {
  sub_container: Container;
  /**
   * List of node type identifiers that can be created in specifically in this container node, but are not normally
   * listed in the menu
   */
  extra_node_types: string[];

  constructor(container: Container) {
    super(container);
    this.description = 'Contains other nodes';
    this.title = 'Folder';
    this.settings['name'] = {
      description: 'Folder name',
      value: 'Folder',
      type: SettingType.STRING,
    };
  }

  shouldRejectNodeType(type: string): boolean {
    return IOT_TYPES.includes(type); // IoT nodes can only be created in special container nodes
  }

  onCreated() {
    this.setupSubContainer();
    this.initializeNameValue();
  }

  onAdded() {
    this.applyTitle();
  }

  onAfterSettingsChange(oldSettings, oldName?: any) {
    this.applyTitle();
  }

  onRemoved() {
    for (let id in this.sub_container._nodes) {
      let node = this.sub_container._nodes[id];
      node.container.remove(node);
    }

    delete Container.containers[this.sub_container.id];
  }

  configure(data, from_db = false, configure_sub_cont = true) {
    super.configure(data);
    try {
      this.sub_container = Container.containers[data.sub_container.id];
      if (!this.sub_container) this.sub_container = new Container(this.side, data.sub_container.id);

      this.sub_container.container_node = this;
      this.sub_container.parent_container_id = this.container.id;
      this.sub_container.parent_id = this.id;

      if (configure_sub_cont) this.sub_container.configure(data.sub_container, true);
    } catch (err) {
      this.debugErr(`ERROR: configure() container node: ${err}`);
    }
  }

  serialize() {
    try {
      let data = super.serialize();
      data.sub_container = this.sub_container.serialize();
      return data;
    } catch (err) {
      this.debugErr(`ERROR: serialize() container node: ${err}`);
    }
  }

  getExtraMenuOptions(renderer, editor) {
    let that = this;
    return [
      {
        content: 'Open',
        callback: function() {
          editor.openContainer(that.sub_container.id);
        },
      },
      null, //null for horizontal line
    ];
  }

  onExecute() {
    if (this.isUpdated) this.isRecentlyActive = true;

    this.sub_container.runStep();
  }

  applyTitle() {
    this.title = this.settings['name'].value;
    this.sub_container.name = this.title;
    this.size = this.computeSize();
  }

  /**
   * Overridable. Called once, after the sub_container has been set up.
   */
  protected getInitialTitle() {
    return `${this.title} ${this.sub_container.id}`;
  }

  private setupSubContainer() {
    if (this.sub_container) {
      this.sub_container = Container.containers[this.sub_container.id];
    } else {
      this.sub_container = new Container(this.side);
    }
    this.sub_container.container_node = this;
    this.sub_container.parent_container_id = this.container.id;
    this.sub_container.parent_id = this.id;
    if (this.container.db) this.container.db.updateLastContainerId(this.sub_container.id);
  }

  private initializeNameValue() {
    this.settings['name'].value = this.getInitialTitle();
  }
}
